import React, { PureComponent } from 'react'
import Constants from 'common/constants'
import cloneDeep from 'lodash/cloneDeep'
import Utils from 'common/utils/utils'

const splitIfValue = (v, append) =>
  append ? v.split(append) : [v === null ? '' : v]

export default class Rule extends PureComponent {
  static displayName = 'Rule'

  static propTypes = {}

  constructor(props) {
    super(props)
    this.state = {
      isLastRule: true,
    }
  }

  renderRule = (rule, i) => {
    const {
      props: {
        operators,
        rule: { conditions: rules },
      },
    } = this
    // setIsLastRule(rules.filter(item => !item.delete).length);
    // const isLastRule = i === rules.length - 1
    this.setState({
      isLastRule: i === rules.length - 1,
    })
    // setIsLastRule(i === rules.length - 1 || rules.filter(item => !item.delete).length === 1)
    // const isLastRule = i === rules.length - 1 || rules.filter(item => !item.delete).length === 1;
    const hasOr = i > 0
    const operatorObj = Utils.findOperator(rule.operator, rule.value, operators)
    const operator = operatorObj && operatorObj.value
    const value =
      typeof rule.value === 'string'
        ? rule.value.replace((operatorObj && operatorObj.append) || '', '')
        : rule.value

    if (rule.delete) {
      return null
    }

    const valuePlaceholder = rule.hideValue
      ? 'Value (N/A)'
      : rule.valuePlaceholder || 'Value'

    const removeButtonWithPermissions = Utils.renderWithPermission(
      this.props.deleteRulePermission,
      Constants.projectPermissions('Admin'),
      <div>
        <button
          data-test={`${this.props['data-test']}-remove`}
          type='button'
          id='remove-feature'
          onClick={() => this.props.onRemove()}
          className='btn btn--with-icon btn--condensed btn--remove'
        >
          <RemoveIcon />
        </button>
      </div>,
    )

    return (
      <div className='rule__row reveal' key={i}>
        {hasOr && (
          <Row className='or-divider'>
            <Row>
              <div className='or-divider__up' />
              Or
              <div className='or-divider__down' />
            </Row>
            <Flex className='or-divider__line' />
          </Row>
        )}
        <Row noWrap className='rule'>
          <Flex>
            <Row>
              <Flex value={20} className='px-1'>
                {/* <Tooltip
                  title={
                    <Input
                      readOnly={this.props.readOnly}
                      data-test={`${this.props['data-test']}-property-${i}`}
                      className='input-container full-width'
                      value={`${rule.property}`}
                      placeholder={
                        operator && operator === 'PERCENTAGE_SPLIT'
                          ? 'Trait (N/A)'
                          : 'Trait *'
                      }
                      onChange={(e) =>
                        this.setRuleProperty(i, 'property', {
                          value: Utils.safeParseEventValue(e),
                        })
                      }
                      disabled={operator && operator === 'PERCENTAGE_SPLIT'}
                    />
                  }
                  place='top'
                >
                  {Constants.strings.USER_PROPERTY_DESCRIPTION}
                </Tooltip> */}
                <Select
                  options={this.props.traits}
                  isMulti={false}
                  placeholder='Select Traits'
                  data-test={`${this.props['data-test']}-property-${i}`}
                  value={
                    rule.property !== ''
                      ? { label: rule.property, value: rule.property }
                      : null
                  }
                  onChange={(e, obj) => {
                    this.setRuleProperty(i, 'property', {
                      value: Utils.safeParseEventValue(e.value),
                    })
                    // this.setRuleProperty(
                    //   i,
                    //   'value',
                    //   {
                    //     value:
                    //       operatorObj && operatorObj.append
                    //         ? `${e.value}${operatorObj.append}`
                    //         : e.value,
                    //   },
                    //   true,
                    // )
                  }}
                  isDisabled={operator && operator === 'PERCENTAGE_SPLIT'}
                />
                {/* <Tooltip
                  title={
                    <>
                    <Multiselect
                      options={this.props.traits} // Options to display in the dropdown
                      displayValue="name"
                    />
                    </>
                  }
                  place='top'
                >
                  {Constants.strings.USER_PROPERTY_DESCRIPTION}
                </Tooltip> */}
              </Flex>
              <Flex value={30} className='px-1 text-center'>
                {this.props.readOnly ? (
                  _.find(operators, { value: operator }).label
                ) : (
                  <Select
                    data-test={`${this.props['data-test']}-operator-${i}`}
                    value={operator && _.find(operators, { value: operator })}
                    onChange={(value) =>
                      this.setRuleProperty(i, 'operator', value)
                    }
                    // isDisabled={true}
                    options={operators}
                  />
                )}
              </Flex>
              <Flex value={15} className='px-1'>
                <Input
                  readOnly={this.props.readOnly}
                  data-test={`${this.props['data-test']}-value-${i}`}
                  className='input-container--flat full-width'
                  value={value || ''}
                  placeholder={valuePlaceholder}
                  disabled={operatorObj && operatorObj.hideValue}
                  onChange={(e) => {
                    const value = Utils.getTypedValue(
                      Utils.safeParseEventValue(e),
                    )
                    this.setRuleProperty(
                      i,
                      'value',
                      {
                        value:
                          operatorObj && operatorObj.append
                            ? `${value}${operatorObj.append}`
                            : value,
                      },
                      true,
                    )
                  }}
                  isValid={Utils.validateRule(rule)}
                />
              </Flex>
            </Row>
            {this.props.showDescription && (
              <Row noWrap className='rule'>
                <textarea
                  readOnly={this.props.readOnly}
                  data-test={`${this.props['data-test']}-description-${i}`}
                  className='full-width borderless'
                  value={`${rule.description || ''}`}
                  placeholder='Condition description (Optional)'
                  onChange={(e) => {
                    const value = Utils.safeParseEventValue(e)
                    this.setRuleProperty(i, 'description', { value }, true)
                  }}
                />
              </Row>
            )}
          </Flex>
          <div>
            <Row noWrap>
              {this.state.isLastRule && !this.props.readOnly ? (
                <ButtonOutline
                  className='ml-2'
                  data-test={`${this.props['data-test']}-or`}
                  type='button'
                  onClick={this.addRule}
                >
                  Or
                </ButtonOutline>
              ) : (
                <div style={{ width: 64 }} />
              )}
              {removeButtonWithPermissions}
            </Row>
          </div>
        </Row>
      </div>
    )
  }
  removeRule = (i) => {
    this.setRuleProperty(i, 'delete', { value: true })
  }

  setRuleProperty = (i, prop, { value }) => {
    const rule = cloneDeep(this.props.rule);
    const { conditions: rules } = rule;
  
    const currentRule = rules[i];
    const prevOperator = this.getOperator(currentRule.operator, currentRule.value) || {};
    const newOperator = this.getNewOperator(prop, value) || {};
  
    this.updateRuleValue(currentRule, prevOperator, newOperator);
    this.applyFormattedValue(currentRule, prop, value);
  
    if (prop === 'operator' && value === 'PERCENTAGE_SPLIT') {
      this.resetPercentageSplit(currentRule);
    }
  
    this.checkForDeletedConditions(rule);
  
    this.props.onChange(rule);
  }
  
  // Helper functions
  
  getOperator = (operator, value) => {
    return Utils.findOperator(operator, value, this.props.operators) || {};
  }
  
  getNewOperator = (prop, value) => {
    return prop !== 'operator'
      ? this.getOperator(prop, value)
      : this.props.operators.find((v) => v.value === value) || {};
  }
  
  updateRuleValue = (rule, prevOperator, newOperator) => {
    if (newOperator.hideValue) {
      rule.value = null;
    }
  
    // Only update rule.value if either prevOperator or newOperator exists
    if (prevOperator.append !== newOperator.append) {
      const prevAppend = prevOperator.append || '';
      const newAppend = newOperator.append || '';
  
      rule.value = splitIfValue(rule.value, prevAppend)[0] + newAppend;
    }
  }
  
  applyFormattedValue = (rule, prop, value) => {
    const formattedValue = value === null ? null : `${value}`;
    const invalidPercentageSplit = this.isInvalidPercentageSplit(prop, rule.operator, value);
  
    if (!invalidPercentageSplit) {
      rule[prop] = prop === 'operator' ? formattedValue.split(':')[0] : formattedValue;
    }
  }
  
  isInvalidPercentageSplit = (prop, operator, value) => {
    return (
      prop === 'value' &&
      operator === 'PERCENTAGE_SPLIT' &&
      (`${value}`?.match(/\D/) || value > 100)
    );
  }
  
  resetPercentageSplit = (rule) => {
    rule.property = '';
    rule.value = '';
  }
  
  checkForDeletedConditions = (rule) => {
    if (!rule.conditions.filter((condition) => !condition.delete).length) {
      rule.delete = true;
    }
  }
  

  addRule = () => {
    const {
      props: {
        rule: { conditions: rules },
      },
    } = this
    this.props.onChange({
      ...this.props.rule,
      conditions: rules.concat([{ ...Constants.defaultRule }]),
    })
  }

  render() {
    const {
      props: {
        rule: { conditions: rules },
      },
    } = this
    return (
      <div>
        <div className='panel panel-without-heading overflow-visible'>
          <div className='panel-content'>{rules.map(this.renderRule)}</div>
        </div>
      </div>
    )
  }
}
