import React, { Component } from 'react'
import withSegmentOverrides from 'common/providers/withSegmentOverrides'
import Constants from 'common/constants'
import data from 'common/data/base/_data'
import ProjectStore from 'common/stores/project-store'
import ConfigProvider from 'common/providers/ConfigProvider'
import FeatureListStore from 'common/stores/feature-list-store'
import {
  Bar,
  BarChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip as RechartsTooltip,
  XAxis,
  YAxis,
} from 'recharts'
import sanitizeHtml from 'sanitize-html'
import Tabs from 'components/base/forms/Tabs'
import TabItem from 'components/base/forms/TabItem'
import SegmentOverrides from 'components/SegmentOverrides'
import AddEditTags from 'components/tags/AddEditTags'
import FlagOwners from 'components/FlagOwners'
import ChangeRequestModal from './ChangeRequestModal'
import Feature from 'components/Feature'
import { ButtonOutline } from 'components/base/forms/Button'
import { setInterceptClose } from 'project/modals'
import classNames from 'classnames'
import InfoMessage from 'components/InfoMessage'
import JSONReference from 'components/JSONReference'
import ErrorMessage from 'components/ErrorMessage'
import Permission from 'common/providers/Permission'
import IdentitySelect from 'components/IdentitySelect'
import store from '../../../common/stores/change-requests-store'
import propTypes from 'prop-types';

const CreateFlag = class extends Component {
  static displayName = 'CreateFlag'

  constructor(props, context) {
    super(props, context)
    const {
      archieved_multivariate_options,
      description,
      enabled,
      feature_state_value,
      hide_from_client,
      is_archived,
      is_server_key_only,
      multivariate_options,
      name,
      tags,
    } = this.props.isEdit
      ? Utils.getFlagValue(
          this.props.projectFlag,
          this.props.environmentFlag,
          this.props.identityFlag,
        )
      : {
          archieved_multivariate_options: [],
          multivariate_options: [],
        }
    const { allowEditDescription } = this.props
    if (this.props.projectFlag) {
      this.userOverridesPage(1)
    }
    this.state = {
      allowEditDescription,
      archieved_multivariate_options: _.cloneDeep(
        archieved_multivariate_options,
      ),
      default_enabled: enabled,
      description,
      enabledIndentity: false,
      enabledSegment: false,
      hide_from_client,
      identityVariations:
        this.props.identityFlag &&
        this.props.identityFlag.multivariate_feature_state_values
          ? _.cloneDeep(
              this.props.identityFlag.multivariate_feature_state_values,
            )
          : [],
      initial_value:
        typeof feature_state_value === 'undefined'
          ? undefined
          : Utils.getTypedValue(feature_state_value),
      is_archived,
      is_server_key_only,
      lastSaved: FeatureListStore.getLastSaved(),
      multivariateErrors: [],
      multivariate_options: _.cloneDeep(multivariate_options),
      name,
      period: 30,
      selectedIdentity: null,
      tab: Utils.fromParam().tab || 0,
      tags: tags || [],
    }
    AppActions.getGroups(AccountStore.getOrganisation().id)

    ES6Component(this)
    this.listenTo(FeatureListStore, 'change', () => {
      this.setState({
        lastSaved: FeatureListStore.getLastSaved(),
      })
    })
  }

  close() {
    closeModal()
  }

  componentDidUpdate(prevProps, prevState) {
    const { identity, environmentVariations, projectFlag, multivariate_options } = this.props;
  
    if (!identity && environmentVariations !== prevProps.environmentVariations) {
      this.updateMultivariateOptions();
    } else if (!identity && this.state.lastSaved !== prevState.lastSaved && projectFlag) {
      this.updateMultivariateOptions();
    }
  }
  
  updateMultivariateOptions() {
    const { projectFlag, environmentVariations, multivariate_options } = this.props;
    
    if (environmentVariations && environmentVariations.length) {
      const currentFlags = FeatureListStore.getProjectFlags();
      const currentOptions = currentFlags
        ? currentFlags.find(flag => flag.id === projectFlag.id)?.multivariate_options || []
        : [];
  
      this.setState({
        multivariate_options: currentOptions.map(v => {
          const matchingVariation = (multivariate_options || environmentVariations)
            .find(e => e.multivariate_feature_option === v.id);
          return {
            ...v,
            default_percentage_allocation: v.default_percentage_allocation ||
              (matchingVariation && matchingVariation.percentage_allocation) || 0,
          };
        }),
      });
    }
  }
  

  onClosing = () => {
    if (this.props.isEdit) {
      return new Promise((resolve) => {
        if (
          this.state.valueChanged ||
          this.state.segmentsChanged ||
          this.state.settingsChanged
        ) {
          openConfirm(
            'Are you sure',
            'Closing this will discard your unsaved changes.',
            () => resolve(true),
            () => resolve(false),
            'Ok',
            'Cancel',
          )
        } else {
          resolve(true)
        }
      })
    }
    return Promise.resolve(true)
  }

  componentDidMount = () => {
    setInterceptClose(this.onClosing)
    if (!this.props.isEdit && !E2E) {
      this.focusTimeout = setTimeout(() => {
        this.input.focus()
        this.focusTimeout = null
      }, 500)
    }
    if (
      !Project.disableAnalytics &&
      this.props.projectFlag &&
      this.props.environmentFlag
    ) {
      this.getFeatureUsage()
    }
  }

  componentWillUnmount() {
    if (this.focusTimeout) {
      clearTimeout(this.focusTimeout)
    }
  }

  userOverridesPage = (page) => {
    if (Utils.getIsEdge()) {
      return
    }
    data
      .get(
        `${Project.api}environments/${
          this.props.environmentId
        }/${Utils.getFeatureStatesEndpoint()}/?anyIdentity=1&feature=${
          this.props.projectFlag.id
        }&page=${page}`,
      )
      .then((userOverrides) => {
        this.setState({
          userOverrides: userOverrides.results,
          userOverridesPaging: {
            count: userOverrides.count,
            currentPage: page,
            next: userOverrides.next,
          },
        })
      })
  }

  getFeatureUsage = () => {
    if (
      Utils.getFlagsmithHasFeature('flag_analytics') &&
      this.props.environmentFlag
    ) {
      AppActions.getFeatureUsage(
        this.props.projectId,
        this.props.environmentFlag.environment,
        this.props.projectFlag.id,
        this.state.period,
      )
    }
  }
  save = (func, isSaving) => {
    const {
      environmentFlag,
      environmentId,
      identity,
      identityFlag,
      projectFlag: _projectFlag,
      segmentOverrides,
    } = this.props
    const {
      default_enabled,
      description,
      hide_from_client,
      initial_value,
      is_archived,
      is_server_key_only,
      name,
    } = this.state
    const projectFlag = {
      skipSaveProjectFeature: this.state.skipSaveProjectFeature,
      ..._projectFlag,
    }
    const hasMultivariate =
      this.props.environmentFlag &&
      this.props.environmentFlag.multivariate_feature_state_values &&
      this.props.environmentFlag.multivariate_feature_state_values.length
    if (identity) {
      !isSaving &&
        name &&
        func({
          environmentFlag,
          environmentId,
          identity,
          identityFlag: Object.assign({}, identityFlag || {}, {
            enabled: default_enabled,
            feature_state_value: hasMultivariate
              ? this.props.environmentFlag.feature_state_value
              : initial_value,
            multivariate_options: this.state.identityVariations,
          }),
          projectFlag,
        })
    } else {
      FeatureListStore.isSaving = true
      FeatureListStore.trigger('change')
      !isSaving &&
        name &&
        func(
          this.props.projectId,
          this.props.environmentId,
          {
            default_enabled,
            description,
            hide_from_client,
            initial_value,
            is_archived,
            is_server_key_only,
            multivariate_options: this.state.multivariate_options,
            name,
            tags: this.state.tags,
          },
          projectFlag,
          environmentFlag,
          segmentOverrides,
        );
    }
  }

  changeSegment = (items) => {
    const { enabledSegment } = this.state
    items.forEach((item) => {
      item.enabled = enabledSegment
    })
    this.props.updateSegments(items)
    this.setState({ enabledSegment: !enabledSegment })
  }

  changeIdentity = (items) => {
    const { environmentId } = this.props
    const { enabledIndentity } = this.state

    Promise.all(
      items.map(
        (item) =>
          new Promise((resolve) => {
            AppActions.changeUserFlag({
              environmentId,
              identity: item.identity.id,
              identityFlag: item.id,
              onSuccess: resolve,
              payload: {
                enabled: enabledIndentity,
                id: item.identity.id,
                value: item.identity.identifier,
              },
            })
          }),
      ),
    ).then(() => {
      this.userOverridesPage(1)
    })

    this.setState({ enabledIndentity: !enabledIndentity })
  }

  toggleUserFlag = ({ enabled, id, identity }) => {
    const { environmentId } = this.props

    AppActions.changeUserFlag({
      environmentId,
      identity: identity.id,
      identityFlag: id,
      onSuccess: () => {
        this.userOverridesPage(1)
      },
      payload: {
        enabled: !enabled,
        id: identity.id,
        value: identity.identifier,
      },
    })
  }

  drawChart = (data) => {
    return data?.length ? (
      <ResponsiveContainer height={400} width='100%'>
        <BarChart data={data}>
          <CartesianGrid strokeDasharray='3 5' />
          <XAxis
            interval={0}
            height={100}
            angle={-90}
            textAnchor='end'
            allowDataOverflow={false}
            dataKey='day'
          />
          <YAxis allowDataOverflow={false} />
          <RechartsTooltip />
          <Bar dataKey={'count'} stackId='a' fill='#6633ff' />
        </BarChart>
      </ResponsiveContainer>
    ) : (
      <div className='text-center'>
        There has been no activity for this flag within the past month. Find out
        about Flag Analytics{' '}
        <a
          target='_blank'
          href='https://docs.flagsmith.com/advanced-use/flag-analytics'
          rel='noreferrer'
        >
          here
        </a>
        .
      </div>
    )
  }

  addItem = () => {
    const { environmentFlag, environmentId, identity, projectFlag } = this.props
    this.setState({ isLoading: true })
    const selectedIdentity = this.state.selectedIdentity.value
    const identities = identity ? identity.identifier : []

    if (!_.find(identities, (v) => v.identifier === selectedIdentity)) {
      data
        .post(
          `${
            Project.api
          }environments/${environmentId}/${Utils.getIdentitiesEndpoint()}/${selectedIdentity}/${Utils.getFeatureStatesEndpoint()}/`,
          {
            enabled: !environmentFlag.enabled,
            feature: projectFlag.id,
            feature_state_value: this.state.initial_value ?? null,
          },
        )
        .then(() => {
          this.setState({
            isLoading: false,
            selectedIdentity: null,
          })
          this.userOverridesPage(1)
        })
        .catch((e) => {
          this.setState({ error: e, isLoading: false })
        })
    } else {
      this.setState({
        isLoading: false,
        selectedIdentity: null,
      })
    }
  }

  addVariation = (value) => {
    const nextId =
      this.state.multivariate_options.length +
      this.state.archieved_multivariate_options.length +
      1

    this.setState({
      multivariate_options: this.state.multivariate_options.concat([
        {
          ...Utils.valueToFeatureState(value ?? ''),
          default_percentage_allocation: 0,
          name: `Variation name ${nextId}`,
          tempId: nextId,
        },
      ]),
      valueChanged: true,
    })
  }

  restoreVariation = (option, index) => {
    data
      .post(
        `${Project.api}projects/${this.props.projectId}/features/${this.props.projectFlag.id}/mv-options/`,
        { ...option, feature: this.props.projectFlag.id },
      )
      .then((res) => {
        this.setState((prevState) => ({
          multivariate_options: [...prevState.multivariate_options, res],
          multivariateErrors: [],
          valueChanged: true,
        }), () => {
          this.forceUpdate();
        });
        this.state.archieved_multivariate_options.splice(index, 1)
        this.setState((prevState) => ({
          archieved_multivariate_options: [...prevState.archieved_multivariate_options],
        }), () => {
          this.forceUpdate();
          AppActions.refreshFeatures(this.props.projectId, this.props.environmentId);
        });
      })
  }

  restoreVariationModal = (option, index) => {
    openConfirm(
      'Please confirm',
      <span>
        This will restore archived variation <strong>{option.name}</strong>.
      </span>,
      () => {
        this.restoreVariation(option, index)
      },
    )
  }

  duplicateVariation = (value) => {
    this.addVariation(value)
  }

  removeVariation = (i, id) => {
    if (this.props.isEdit && this.state.multivariate_options[i].custom_id) {
      data
        .delete(
          `${Project.api}projects/${this.props.projectId}/features/${this.props.projectFlag.id}/mv-options/${id}/`,
        )
        .then(() => {
          store.saving()
          data
            .get(
              `${Project.api}projects/${this.props.projectId}/features/${this.props.projectFlag.id}/`,
            )
            .then((res) => {
              this.state.multivariate_options.splice(i, 1)
              store.model.lastSaved = new Date().valueOf()
              store.saved()
              this.setState((prevState) => ({
                archieved_multivariate_options: res.archieved_multivariate_options,
                multivariateErrors: [],
                multivariate_options: prevState.multivariate_options,  // Using prevState to reference the correct previous state
                valueChanged: true,
              }), () => {
                this.forceUpdate();  // If needed, but usually not required
                AppActions.refreshFeatures(this.props.projectId, this.props.environmentId);  // Callback after state update
              });
            })
            .catch((e) => API.ajaxHandler(FeatureListStore, e))
        })
    } else {
      if (this.state.multivariate_options[i]) {
        const newOptions = [...this.state.multivariate_options]
        newOptions.splice(i, 1)

        // Filter out options without a custom_id
        const optionsWithoutCustomId = newOptions.filter(
          (option) => !option.custom_id
        );

        const optionsWithCustomId = newOptions.filter(
          (option) => option.custom_id
        );

        let nextId = 1;
        if (this.props.isEdit) {
          nextId = 
            optionsWithCustomId.length +
            this.state.archieved_multivariate_options.length
            + 1
        }

        // Update only the options without custom_id
        const updatedOptionsWithoutCustomId = optionsWithoutCustomId.map((option) => ({
          ...option,
          name: `Variation name ${nextId}`,
          tempId: nextId++,
        }));

        // Merge updated options back into newOptions
        const finalOptions = newOptions.map((option) =>
          option.custom_id
            ? option // Leave options with custom_id unchanged
            : updatedOptionsWithoutCustomId.shift() // Replace options without custom_id with updated ones
        );

        this.setState({
          multivariate_options: finalOptions,
          valueChanged: true,
        });

        this.forceUpdate()
        store.trigger('refresh')
      }
    }

    this.state.valueChanged = true
  }

  updateVariation = (i, e, environmentVariations) => {
    this.setState({ multivariateErrors: [] })

    this.props.onEnvironmentVariationsChange(environmentVariations)
    this.state.multivariate_options[i] = e
    this.state.valueChanged = true
    this.forceUpdate()
  }

  getVariationOptionValue = (option) => {
    return JSON.stringify(Utils.featureStateToValue(option)).trim()
  }

  validateVariationNamesAndValues = () => {
    // There is 3 possible text error messages
    // 1. Duplicate name current & archived - just message. Collect names
    // 2. Duplicate value from current - just message. Collect names
    // 3. Duplicate value from archive - message + button. New message for each

    const { archieved_multivariate_options, multivariate_options } = this.state
    const allOptions = [
      ...archieved_multivariate_options,
      ...multivariate_options,
    ]

    const unsavedVariationsMapByValue = {}
    const savedOptions = multivariate_options.filter((option) => {
      if (!option.custom_id) {
        unsavedVariationsMapByValue[this.getVariationOptionValue(option)] =
          option

        return false
      }

      return true
    })

    const duplicateValuesCurrent = savedOptions
      .filter((option) => {
        const value = this.getVariationOptionValue(option)

        return unsavedVariationsMapByValue[value]
      })
      .map((option) => option.name)

    const duplicateNamesError = allOptions
      .map((option) => option.name)
      .filter((item, index, array) => array.indexOf(item) !== index)

    const duplicateValuesArchived = archieved_multivariate_options.filter(
      (option) => {
        const value = this.getVariationOptionValue(option)
        return unsavedVariationsMapByValue[value]
      },
    )

    const controlValueErrors = multivariate_options.find((option) => {
      const controlValue = JSON.stringify(this.state.initial_value)
      const currentOption = this.getVariationOptionValue(option)

      if (controlValue === currentOption) {
        return option
      }
    })

    const errorsArray = []

    if (duplicateNamesError.length) {
      errorsArray.push(
        `Error: Variation ${[...new Set(duplicateNamesError)].join(
          ', ',
        )} with same name already exists`,
      )
    }

    if (duplicateValuesCurrent.length) {
      errorsArray.push(
        `Error: Variation ${[...new Set(duplicateValuesCurrent)].join(
          ', ',
        )} with same value already exists`,
      )
    }

    if (controlValueErrors) {
      errorsArray.push(
        `Error: Control value is used in variation ${controlValueErrors.name}`,
      )
    }

    return [
      ...errorsArray,
      ...duplicateValuesArchived.map((option) => {
        const index = option.index
        delete option.index

        return (
          <div key={index}>
            Duplicate value <strong>{option.name}</strong> founded in archive.{' '}
            <Button onClick={() => this.restoreVariation(option, index)}>
              Restore?
            </Button>
          </div>
        )
      }),
    ]
  }

  segmentOverridesIsValid = () => {
    const { segmentOverrides } = this.props

    const controlPercent = segmentOverrides?.filter((segment) => {
      return Utils.calculateControl(segment.multivariate_options) < 0
    })

    return (
      controlPercent?.length ||
      segmentOverrides?.filter(
        (segment) =>
          !(
            segment?.value !== null &&
            segment?.value !== undefined &&
            `${segment?.value}`
          ),
      )?.length
    )
  }

  validateValues = () => {
    const { initial_value, multivariate_options } = this.state

    const isMultivariateValid = !!multivariate_options.find((variate) => {
      const value = Utils.featureStateToValue(variate)
      return !(value !== null && `${value}`)
    })

    const emptyNames = multivariate_options.some((option) => option.name === '')
    if (emptyNames) return true

    // if no initial value && state value and multivariate optionsare set - it's an identity
    if (
      (initial_value === null || initial_value === undefined) &&
      this.props.environmentFlag?.feature_state_value !== null &&
      this.props.environmentFlag?.feature_state_value !== undefined &&
      `${this.props.environmentFlag?.feature_state_value}` &&
      this.props.environmentFlag?.multivariate_feature_state_values?.length
    ) {
      return false
    }

    const controlValue = Utils.calculateControl(multivariate_options)
    return (
      (!!multivariate_options &&
        multivariate_options.length &&
        controlValue < 0) ||
      isMultivariateValid ||
      !(
        initial_value !== null &&
        initial_value !== undefined &&
        `${initial_value}`
      )
    )
  }

  /* Added for refactoring */

  validateRegex(name, regex, isEdit) {
    try {
      return !isEdit && name && regex ? name.match(new RegExp(regex)) : true;
    } catch {
      return false;
    }
  }
  
  onSaveHandler = () => {
    const { isEdit, identity, projectId, environmentId } = this.props;
    if (!isEdit || identity) this.close();
    AppActions.refreshFeatures(projectId, environmentId);
  };
  
  createSaveHandlers = (actions, isSaving) => {
    const { editFeatureSegments, editFeatureSettings, editFeatureValue, createFlag, createChangeRequest } = actions;
    return {
      saveFeatureValue: (schedule) => this.saveFeature(schedule, createChangeRequest, editFeatureValue, isSaving),
      saveSettings: () => this.save(editFeatureSettings, isSaving),
      saveFeatureSegments: () => this.save(editFeatureSegments, isSaving),
      onCreateFeature: () => this.save(createFlag, isSaving),
    };
  };
  
  renderSettings(description, projectFlag) {
    return (
      <Permission level='project' permission='ADMIN' id={this.props.projectId}>
        {({ permission: projectAdmin }) => (
          <>
            {projectAdmin && projectFlag && <FlagOwners projectId={this.props.projectId} id={projectFlag.id} />}
            <FormGroup className='mb-4 mr-3 ml-3'>
              <InputGroup
                value={description}
                data-test='featureDesc'
                inputProps={{ className: 'full-width', name: 'featureDesc' }}
                onChange={(e) => this.setState({ description: Utils.safeParseEventValue(e), settingsChanged: true })}
                type='text'
                title='Description (optional)'
                placeholder="e.g. 'This determines what size the header is' "
              />
            </FormGroup>
          </>
        )}
      </Permission>
    );
  }
  
  renderValueSettings({ error, projectAdmin, createFeature, hideValue }) {
    return (
      <FormGroup className='mb-4 mr-3 mt-2 ml-3'>
        <InputGroup
          ref={(e) => (this.input = e)}
          data-test='featureID'
          inputProps={{ className: 'full-width', maxLength: Constants.forms.maxLength.FEATURE_ID, name: 'featureID', readOnly: this.props.isEdit }}
          value={this.state.name}
          onChange={(e) => this.handleNameChange(e)}
          isValid={!!this.state.name && this.regexValid}
          type='text'
          title='ID*'
          placeholder='E.g. header_size'
        />
        {error && <ErrorMessage error={error.name?.[0]} />}
      </FormGroup>
    );
  }
  
  handleNameChange(e) {
    const newName = Utils.safeParseEventValue(e).replace(/ /g, '_');
    const caseSensitive = Utils.getFlagsmithHasFeature('case_sensitive_flags') && ProjectStore.model?.only_allow_lower_case_feature_names;
    this.setState({ name: caseSensitive ? newName.toLowerCase() : newName });
  }
  
  renderTabs(project, saveFeatureValue, is4Eyes, changeRequest, Value, Settings) {
    return (
      <Tabs value={this.state.tab} onChange={(tab) => this.setState({ tab })}>
        <TabItem data-test='value' tabLabel={this.renderTabLabel('Value', this.state.valueChanged)}>
          <FormGroup className='mr-3 ml-3'>
            <Panel title={this.renderFeaturePanelTitle(project)}>
              {Value}
              {this.props.isEdit && this.renderJSONReference()}
            </Panel>
            {this.renderSaveButton(saveFeatureValue, is4Eyes, changeRequest)}
          </FormGroup>
        </TabItem>
        {this.renderSegmentOverridesTab(is4Eyes)}
        {!this.props.identity && this.renderIdentityOverridesTab()}
      </Tabs>
    );
  }
  
  renderTabLabel(title, hasChanged) {
    return (
      <Row className='justify-content-center'>
        {title}
        {hasChanged && <div className='unread ml-2 px-1'>{'*'}</div>}
      </Row>
    );
  }
  
  renderFeaturePanelTitle(project) {
    const environmentName = _.find(project.environments, { api_key: this.props.environmentId })?.name;
    return (
      <Tooltip title={<h6 className='mb-0'>Feature Value <span className='icon ion-ios-information-circle' /></h6>} place='top'>
        {Constants.strings.ENVIRONMENT_OVERRIDE_DESCRIPTION(environmentName)}
      </Tooltip>
    );
  }
  
  renderJSONReference() {
    return (
      <>
        <JSONReference showNamesButton title={'Feature'} json={this.props.projectFlag} />
        <JSONReference title={'Feature state'} json={this.props.environmentFlag} />
      </>
    );
  }
  
  renderSaveButton(saveFeatureValue, is4Eyes, changeRequest) {
    return (
      <Permission level='environment' permission={Utils.getManageFeaturePermission(is4Eyes, this.props.identity)} id={this.props.environmentId}>
        {({ permission: savePermission }) =>
          Utils.renderWithPermission(savePermission, Constants.environmentPermissions('Write'), (
            <div className='text-right'>
              <Button onClick={() => saveFeatureValue()} disabled={!savePermission || this.state.isSaving || !this.state.name || this.isValuesValid()}>
                {this.state.isSaving ? (changeRequest ? 'Updating Change Request' : 'Creating Change Request') : 'Update Feature'}
              </Button>
            </div>
          ))
        }
      </Permission>
    );
  }
  
  renderSegmentOverridesTab(is4Eyes) {
    return (
      <TabItem data-test='segment_overrides' tabLabel={this.renderTabLabel('Segments', this.state.segmentsChanged)}>
        <FormGroup className='mb-4 mr-3 ml-3'>
          <Panel title={this.renderSegmentOverridesTitle()}>
            <SegmentOverrides {...this.props} onChange={(v) => this.handleSegmentChange(v)} />
            {!this.state.showCreateSegment && this.renderSegmentOverridesFooter(is4Eyes)}
          </Panel>
        </FormGroup>
      </TabItem>
    );
  }
  
  renderSegmentOverridesTitle() {
    return (
      <Tooltip title={<h6 className='mb-0'>Segment Overrides <span className='icon ion-ios-information-circle' /></h6>} place='right'>
        {Constants.strings.SEGMENT_OVERRIDES_DESCRIPTION}
      </Tooltip>
    );
  }
  
  handleSegmentChange(v) {
    this.setState({ segmentsChanged: true });
    this.props.updateSegments(v);
  }
  
  renderSegmentOverridesFooter(is4Eyes) {
    return (
      <div>
        <p className='text-right mt-4'>
          {is4Eyes ? 'This will create a change request for the environment' : 'This will update the segment overrides for the app'}
        </p>
        <Permission level='environment' permission={Utils.getManageFeaturePermission(is4Eyes, this.props.identity)} id={this.props.environmentId}>
          {({ permission: savePermission }) =>
            Utils.renderWithPermission(savePermission, Constants.environmentPermissions('Write'), (
              <div className='text-right'>
                <Button onClick={this.saveFeatureSegments} disabled={this.state.isSaving || !this.state.name || this.segmentOverridesIsValid() || !savePermission}>
                  {this.state.isSaving ? 'Updating' : 'Update Segment Overrides'}
                </Button>
              </div>
            ))
          }
        </Permission>
      </div>
    );
  }
  
  renderCreateFeatureTab(Value, createFeature, regexValid, onCreateFeature) {
    return (
      <div className={!this.props.isEdit ? 'create-feature-tab' : ''}>
        {Value}
        <div className='text-right mr-3'>
          <Button onClick={onCreateFeature} disabled={this.state.isSaving || !this.state.name || this.isValuesValid() || !regexValid}>
            {this.state.isSaving ? 'Creating' : 'Create Feature'}
          </Button>
        </div>
      </div>
    );
  }
  
  renderIdentitySection(identityName, saveFeatureValue, isSaving, name, isValuesValid) {
    return (
      <div className='pr-3'>
        <p className='text-left ml-3'>
          This will update the feature value for the device <strong>{identityName}</strong>. Any segment overrides for this feature will now be ignored.
        </p>
        <div className='text-right mb-2'>
          <Button onClick={() => saveFeatureValue()} disabled={isSaving || !name || isValuesValid}>
            {isSaving ? 'Updating' : 'Update Feature'}
          </Button>
        </div>
      </div>
    );
  }

  /* End of refactoring */

  render() {
    const {
      archieved_multivariate_options,
      default_enabled,
      description,
      enabledIndentity,
      enabledSegment,
      hide_from_client,
      initial_value,
      multivariate_options,
      multivariateErrors,
      name,
    } = this.state

    const isValuesValid = this.validateValues()
    const segmentOverridesIsValid = this.segmentOverridesIsValid()
    const FEATURE_ID_MAXLENGTH = Constants.forms.maxLength.FEATURE_ID
    const { identity, identityName, isEdit, projectFlag } = this.props
    const Provider = identity ? IdentityProvider : FeatureListProvider
    const environmentVariations = this.props.environmentVariations
    const environment = ProjectStore.getEnvironment(this.props.environmentId)
    const is4Eyes =
      !!environment &&
      Utils.changeRequestsEnabled(environment.minimum_change_request_approvals)
    const canSchedule = Utils.getPlansPermission('SCHEDULE_FLAGS')
    const is4EyesSegmentOverrides =
      is4Eyes && Utils.getFlagsmithHasFeature('4eyes_segment_overrides') //
    const project = ProjectStore.model
    const caseSensitive =
      Utils.getFlagsmithHasFeature('case_sensitive_flags') &&
      project?.only_allow_lower_case_feature_names
    const regex = project?.feature_name_regex
    const existingChangeRequest = this.props.changeRequest
    const hideIdentityOverridesTab = Utils.getShouldHideIdentityOverridesTab()
    const noPermissions = this.props.noPermissions
    let regexValid = true
    try {
      if (!isEdit && name && regex) {
        regexValid = name.match(new RegExp(regex))
      }
    } catch (e) {
      regexValid = false
    }
    const Settings = (projectAdmin, createFeature) => (
      <>
        {!identity && projectFlag && (
          <Permission
            level='project'
            permission='ADMIN'
            id={this.props.projectId}
          >
            {({ permission: projectAdmin }) =>
              projectAdmin && (
                <FormGroup className='mb-4 mr-3 ml-3'>
                  <FlagOwners
                    projectId={this.props.projectId}
                    id={projectFlag.id}
                  />
                </FormGroup>
              )
            }
          </Permission>
        )}
        <FormGroup className='mb-4 mr-3 ml-3'>
          <InputGroup
            value={description}
            data-test='featureDesc'
            inputProps={{
              className: 'full-width',
              name: 'featureDesc',
            }}
            onChange={(e) =>
              this.setState({
                description: Utils.safeParseEventValue(e),
                settingsChanged: true,
              })
            }
            ds
            type='text'
            title={identity ? 'Description' : 'Description (optional)'}
            placeholder="e.g. 'This determines what size the header is' "
          />
        </FormGroup>

        {!identity && isEdit && (
          <FormGroup className='mb-4 mr-3 ml-3'>
            <InputGroup
              value={description}
              component={
                <Switch
                  checked={this.state.is_archived}
                  onChange={(is_archived) =>
                    this.setState({ is_archived, settingsChanged: true })
                  }
                />
              }
              onChange={(e) =>
                this.setState({ description: Utils.safeParseEventValue(e) })
              }
              type='text'
              title='Archived'
              tooltip='Archiving a flag allows you to filter out flags from the Flagsmith dashboard that are no longer relevant.<br/>An archived flag will still return as normal in all SDK endpoints.'
              placeholder="e.g. 'This determines what size the header is' "
            />
          </FormGroup>
        )}

        {!identity && Utils.getFlagsmithHasFeature('hide_flag') && (
          <FormGroup className='mb-4 mr-3 ml-3'>
            <Tooltip
              title={
                <label className='cols-sm-2 control-label'>
                  Hide from SDKs{' '}
                  <span className='icon ion-ios-information-circle' />
                </label>
              }
              place='right'
            >
              {Constants.strings.HIDE_FROM_SDKS_DESCRIPTION}
            </Tooltip>
            <div>
              <Switch
                data-test='toggle-feature-button'
                defaultChecked={hide_from_client}
                checked={hide_from_client}
                onChange={(hide_from_client) =>
                  this.setState({ hide_from_client })
                }
              />
            </div>
          </FormGroup>
        )}
      </>
    )

    const Value = (error, projectAdmin, createFeature, hideValue) => (
      <>
        {!isEdit && (
          <FormGroup className='mb-4 mr-3 mt-2 ml-3'>
            <InputGroup
              ref={(e) => (this.input = e)}
              data-test='featureID'
              inputProps={{
                className: 'full-width',
                maxLength: FEATURE_ID_MAXLENGTH,
                name: 'featureID',
                readOnly: isEdit,
              }}
              value={name}
              onChange={(e) => {
                const newName = Utils.safeParseEventValue(e).replace(/ /g, '_')
                this.setState({
                  name: caseSensitive ? newName.toLowerCase() : newName,
                })
              }}
              isValid={!!name && regexValid}
              type='text'
              title={
                <>
                  {isEdit ? 'ID' : 'ID*'}
                  {!!regex && !isEdit && (
                    <div className='mt-2'>
                      {' '}
                      <InfoMessage>
                        {' '}
                        This must conform to the regular expression{' '}
                        <code>{regex}</code>
                      </InfoMessage>
                    </div>
                  )}
                </>
              }
              placeholder='E.g. header_size'
            />
            <ErrorMessage error={error?.name?.[0]} />
          </FormGroup>
        )}

        {identity && description && (
          <FormGroup className='mb-4 mt-2 mr-3 ml-3'>
            <InputGroup
              value={description}
              data-test='featureDesc'
              inputProps={{
                className: 'full-width',
                name: 'featureDesc',
                readOnly: !!identity || noPermissions,
                valueChanged: true,
              }}
              onChange={(e) =>
                this.setState({ description: Utils.safeParseEventValue(e) })
              }
              type='text'
              title={identity ? 'Description' : 'Description (optional)'}
              placeholder='No description'
            />
          </FormGroup>
        )}
        {!hideValue && (
          <div className={identity && !description ? 'mt-2' : ''}>
            <Feature
              archieved_multivariate_options={archieved_multivariate_options}
              duplicateVariation={this.duplicateVariation}
              restoreVariation={this.restoreVariationModal}
              readOnly={noPermissions}
              hide_from_client={hide_from_client}
              multivariateErrors={multivariateErrors}
              multivariate_options={multivariate_options}
              environmentVariations={environmentVariations}
              isEdit={isEdit}
              error={error?.initial_value?.[0]}
              canCreateFeature={createFeature}
              identity={identity}
              removeVariation={this.removeVariation}
              updateVariation={this.updateVariation}
              addVariation={this.addVariation}
              checked={default_enabled}
              value={initial_value}
              identityVariations={this.state.identityVariations}
              onChangeIdentityVariations={(identityVariations) => {
                this.setState({ identityVariations, valueChanged: true })
              }}
              environmentFlag={this.props.environmentFlag}
              projectFlag={projectFlag}
              onValueChange={(e) => {
                const initial_value = Utils.getTypedValue(
                  sanitizeHtml(Utils.safeParseEventValue(e), {
                    allowedAttributes: {},
                    allowedTags: [],
                  }),
                )
                this.setState({ initial_value, valueChanged: true })
              }}
              onCheckedChange={(default_enabled) =>
                this.setState({ default_enabled })
              }
            />
          </div>
        )}
        {!isEdit && !identity && Settings(projectAdmin, createFeature)}
      </>
    )
    return (
      <ProjectProvider id={this.props.projectId}>
        {({ project }) => (
          <Provider
            onSave={() => {
              if (!isEdit || identity) {
                this.close()
              }
              AppActions.refreshFeatures(
                this.props.projectId,
                this.props.environmentId,
              )
            }}
          >
            {(
              { error, isSaving, usageData },
              {
                createChangeRequest,
                createFlag,
                editFeatureSegments,
                editFeatureSettings,
                editFeatureValue,
              },
            ) => {
              const saveFeatureValue = (schedule) => {
                const optionErrors = this.validateVariationNamesAndValues()
                if (!this.props.identity && optionErrors.length) {
                  this.setState({ multivariateErrors: optionErrors })
                  return
                }
                this.setState({ valueChanged: false })
                if ((is4Eyes || schedule) && !identity) {
                  openModal2(
                    schedule
                      ? 'New Scheduled Flag Update'
                      : this.props.changeRequest
                      ? 'Update Change Request'
                      : 'New Change Request',
                    <ChangeRequestModal
                      showAssignees={is4Eyes}
                      changeRequest={this.props.changeRequest}
                      onSave={({
                        approvals,
                        description,
                        live_from,
                        title,
                      }) => {
                        closeModal2()
                        this.save(
                          (
                            projectId,
                            environmentId,
                            flag,
                            projectFlag,
                            environmentFlag,
                            segmentOverrides,
                          ) => {
                            createChangeRequest(
                              projectId,
                              environmentId,
                              flag,
                              projectFlag,
                              environmentFlag,
                              segmentOverrides,
                              {
                                approvals,
                                description,
                                featureStateId:
                                  this.props.changeRequest &&
                                  this.props.changeRequest.feature_states[0].id,
                                id:
                                  this.props.changeRequest &&
                                  this.props.changeRequest.id,
                                live_from,
                                multivariate_options: this.props
                                  .multivariate_options
                                  ? this.props.multivariate_options.map((v) => {
                                      const matching =
                                        this.state.multivariate_options.find(
                                          (m) =>
                                            m.id ===
                                            v.multivariate_feature_option,
                                        )
                                      return {
                                        ...v,
                                        percentage_allocation:
                                          matching.default_percentage_allocation,
                                      }
                                    })
                                  : this.state.multivariate_options,
                                title,
                              },
                              !is4Eyes,
                            )
                          },
                        )
                      }}
                    />,
                  )
                } else if (
                  document.getElementById('language-validation-error')
                ) {
                  openConfirm(
                    'Validation error',
                    'Your remote config value does not pass validation for the language you have selected. Are you sure you wish to save?',
                    () => {
                      this.save(editFeatureValue, isSaving)
                    },
                    null,
                    'Save',
                    'Cancel',
                  )
                } else {
                  this.save(editFeatureValue, isSaving)
                }
              }

              const saveSettings = () => {
                this.setState({ settingsChanged: false })
                this.save(editFeatureSettings, isSaving)
              }

              const saveFeatureSegments = () => {
                this.setState({ segmentsChanged: false })

                this.save(editFeatureSegments, isSaving)
              }

              const onCreateFeature = () => {
                this.save(createFlag, isSaving)
              }

              return (
                <Permission
                  level='project'
                  permission='CREATE_FEATURE'
                  id={this.props.projectId}
                >
                  {({ permission: createFeature }) => (
                    <Permission
                      level='project'
                      permission='ADMIN'
                      id={this.props.projectId}
                    >
                      {({ permission: projectAdmin }) => {
                        this.state.skipSaveProjectFeature = !createFeature
                        return (
                          <div
                            id='create-feature-modal'
                            data-test='create-feature-modal'
                          >
                            {isEdit && !identity ? (
                              <Tabs
                                value={this.state.tab}
                                onChange={(tab) => this.setState({ tab })}
                              >
                                <TabItem
                                  data-test='value'
                                  tabLabel={
                                    <Row className='justify-content-center'>
                                      Value{' '}
                                      {this.state.valueChanged && (
                                        <div className='unread ml-2 px-1'>
                                          {'*'}
                                        </div>
                                      )}
                                    </Row>
                                  }
                                >
                                  <FormGroup className='mr-3 ml-3'>
                                    <Panel
                                      title={
                                        <Tooltip
                                          title={
                                            <h6 className='mb-0'>
                                              Feature Value{' '}
                                              <span className='icon ion-ios-information-circle' />
                                            </h6>
                                          }
                                          place='top'
                                        >
                                          {Constants.strings.ENVIRONMENT_OVERRIDE_DESCRIPTION(
                                            _.find(project.environments, {
                                              api_key: this.props.environmentId,
                                            }).name,
                                          )}
                                        </Tooltip>
                                      }
                                    >
                                      {Value(
                                        error,
                                        projectAdmin,
                                        createFeature,
                                      )}

                                      {isEdit && (
                                        <>
                                          <JSONReference
                                            showNamesButton
                                            title={'Feature'}
                                            json={projectFlag}
                                          />
                                          <JSONReference
                                            title={'Feature state'}
                                            json={this.props.environmentFlag}
                                          />
                                        </>
                                      )}
                                    </Panel>

                                    <Permission
                                      level='environment'
                                      permission={Utils.getManageFeaturePermission(
                                        is4Eyes,
                                        identity,
                                      )}
                                      id={this.props.environmentId}
                                    >
                                      {({ permission: savePermission }) =>
                                        Utils.renderWithPermission(
                                          savePermission,
                                          Constants.environmentPermissions(
                                            'Write',
                                          ),
                                          <div className='text-right'>
                                            {is4Eyes ? (
                                              <Button
                                                onClick={() =>
                                                  saveFeatureValue()
                                                }
                                                type='button'
                                                data-test='update-feature-btn'
                                                id='update-feature-btn'
                                                disabled={
                                                  !savePermission ||
                                                  isSaving ||
                                                  !name ||
                                                  isValuesValid
                                                }
                                              >
                                                {isSaving
                                                  ? existingChangeRequest
                                                    ? 'Updating Change Request'
                                                    : 'Creating Change Request'
                                                  : existingChangeRequest
                                                  ? 'Update Change Request'
                                                  : 'Create Change Request'}
                                              </Button>
                                            ) : (
                                              <Button
                                                onClick={() =>
                                                  saveFeatureValue()
                                                }
                                                type='button'
                                                data-test='update-feature-btn'
                                                id='update-feature-btn'
                                                disabled={
                                                  isSaving ||
                                                  !name ||
                                                  isValuesValid ||
                                                  !savePermission ||
                                                  !!multivariateErrors.length
                                                }
                                              >
                                                {isSaving
                                                  ? 'Updating'
                                                  : 'Update Feature Value'}
                                              </Button>
                                            )}
                                          </div>,
                                        )
                                      }
                                    </Permission>
                                  </FormGroup>
                                </TabItem>
                                {!existingChangeRequest && (
                                  <TabItem
                                    data-test='segment_overrides'
                                    tabLabel={
                                      <Row className='justify-content-center'>
                                        Segments{' '}
                                        {this.state.segmentsChanged && (
                                          <div className='unread ml-2 px-2'>
                                            *
                                          </div>
                                        )}
                                      </Row>
                                    }
                                  >
                                    {!identity && isEdit && (
                                      <FormGroup className='mb-4 mr-3 ml-3'>
                                        <div>
                                          <Panel
                                            icon='ion-ios-settings'
                                            title={
                                              <Tooltip
                                                title={
                                                  <h6 className='mb-0'>
                                                    Segment Overrides{' '}
                                                    <span className='icon ion-ios-information-circle' />
                                                  </h6>
                                                }
                                                place='right'
                                              >
                                                {
                                                  Constants.strings
                                                    .SEGMENT_OVERRIDES_DESCRIPTION
                                                }
                                              </Tooltip>
                                            }
                                            action={
                                              !this.state.showCreateSegment &&
                                              !noPermissions && (
                                                <Button
                                                  onClick={() =>
                                                    this.changeSegment(
                                                      this.props
                                                        .segmentOverrides,
                                                    )
                                                  }
                                                  type='button'
                                                  className={`btn--outline${
                                                    enabledSegment ? '' : '-red'
                                                  }`}
                                                >
                                                  {enabledSegment
                                                    ? 'Enable All'
                                                    : 'Disable All'}
                                                </Button>
                                              )
                                            }
                                          >
                                            {this.props.segmentOverrides ? (
                                              <SegmentOverrides
                                                readOnly={noPermissions}
                                                showEditSegment
                                                showCreateSegment={
                                                  this.state.showCreateSegment
                                                }
                                                setShowCreateSegment={(
                                                  showCreateSegment,
                                                ) =>
                                                  this.setState({
                                                    showCreateSegment,
                                                  })
                                                }
                                                feature={projectFlag.id}
                                                projectId={this.props.projectId}
                                                multivariateOptions={
                                                  multivariate_options
                                                }
                                                environmentId={
                                                  this.props.environmentId
                                                }
                                                value={
                                                  this.props.segmentOverrides
                                                }
                                                controlValue={initial_value}
                                                onChange={(v) => {
                                                  this.setState({
                                                    segmentsChanged: true,
                                                  })
                                                  this.props.updateSegments(v)
                                                }}
                                              />
                                            ) : (
                                              <div className='text-center'>
                                                <Loader />
                                              </div>
                                            )}
                                          </Panel>
                                          {!this.state.showCreateSegment && (
                                            <div>
                                              <p className='text-right mt-4'>
                                                {is4Eyes &&
                                                is4EyesSegmentOverrides
                                                  ? 'This will create a change request for the environment'
                                                  : 'This will update the segment overrides for the app'}{' '}
                                              </p>
                                              <Permission
                                                level='environment'
                                                permission={Utils.getManageFeaturePermission(
                                                  is4Eyes,
                                                  identity,
                                                )}
                                                id={this.props.environmentId}
                                              >
                                                {({
                                                  permission: savePermission,
                                                }) =>
                                                  Utils.renderWithPermission(
                                                    savePermission,
                                                    Constants.environmentPermissions(
                                                      'Write',
                                                    ),
                                                    <div className='text-right'>
                                                      <Button
                                                        onClick={
                                                          saveFeatureSegments
                                                        }
                                                        type='button'
                                                        data-test='update-feature-segments-btn'
                                                        id='update-feature-segments-btn'
                                                        disabled={
                                                          isSaving ||
                                                          !name ||
                                                          segmentOverridesIsValid ||
                                                          !savePermission
                                                        }
                                                      >
                                                        {isSaving
                                                          ? 'Updating'
                                                          : 'Update Segment Overrides'}
                                                      </Button>
                                                    </div>,
                                                  )
                                                }
                                              </Permission>
                                            </div>
                                          )}
                                        </div>
                                      </FormGroup>
                                    )}
                                  </TabItem>
                                )}

                                {!identity &&
                                  isEdit &&
                                  !existingChangeRequest &&
                                  !hideIdentityOverridesTab && (
                                    <TabItem
                                      data-test='identity_overrides'
                                      tabLabel='Identity Overrides'
                                    >
                                      <Permission
                                        level='project'
                                        permission='MANAGE_IDENTITY_OVERRIDES'
                                        id={this.props.projectId}
                                      >
                                        {({
                                          permission: canManageOverrides,
                                        }) => (
                                          <FormGroup className='mb-4 mr-3 ml-3'>
                                            <PanelSearch
                                              id='users-list'
                                              title={
                                                <Tooltip
                                                  title={
                                                    <h6 className='mb-0'>
                                                      Identity Overrides{' '}
                                                      <span className='icon ion-ios-information-circle' />
                                                    </h6>
                                                  }
                                                  place='right'
                                                >
                                                  {
                                                    Constants.strings
                                                      .IDENTITY_OVERRIDES_DESCRIPTION
                                                  }
                                                </Tooltip>
                                              }
                                              action={
                                                <Button
                                                  onClick={() =>
                                                    this.changeIdentity(
                                                      this.state.userOverrides,
                                                    )
                                                  }
                                                  type='button'
                                                  className={`btn--outline${
                                                    enabledIndentity
                                                      ? ''
                                                      : '-red'
                                                  }`}
                                                >
                                                  {enabledIndentity
                                                    ? 'Enable All'
                                                    : 'Disable All'}
                                                </Button>
                                              }
                                              icon='ion-md-person'
                                              items={this.state.userOverrides}
                                              paging={
                                                this.state.userOverridesPaging
                                              }
                                              renderSearchWithNoResults
                                              nextPage={() =>
                                                this.userOverridesPage(
                                                  this.state.userOverridesPaging
                                                    .currentPage + 1,
                                                )
                                              }
                                              prevPage={() =>
                                                this.userOverridesPage(
                                                  this.state.userOverridesPaging
                                                    .currentPage - 1,
                                                )
                                              }
                                              goToPage={(page) =>
                                                this.userOverridesPage(page)
                                              }
                                              searchPanel={
                                                <div className='text-center mt-2 mb-2'>
                                                  <Flex className='text-left'>
                                                    <IdentitySelect
                                                      isEdge={false}
                                                      ignoreIds={this.state.userOverrides?.map(
                                                        (v) => v.identity?.id,
                                                      )}
                                                      environmentId={
                                                        this.props.environmentId
                                                      }
                                                      data-test='select-identity'
                                                      placeholder='Create an Identity Override...'
                                                      value={
                                                        this.state
                                                          .selectedIdentity
                                                      }
                                                      disabled={
                                                        !canManageOverrides
                                                      }
                                                      onChange={(
                                                        selectedIdentity,
                                                      ) =>
                                                        this.setState(
                                                          { selectedIdentity },
                                                          this.addItem,
                                                        )
                                                      }
                                                    />
                                                  </Flex>
                                                </div>
                                              }
                                              renderRow={({
                                                enabled,
                                                feature_state_value,
                                                id,
                                                identity,
                                              }) => (
                                                <Row
                                                  space
                                                  className='list-item cursor-pointer'
                                                  key={id}
                                                >
                                                  <Flex
                                                    onClick={() => {
                                                      window.open(
                                                        `${document.location.origin}/#/project/${this.props.projectId}/environment/${this.props.environmentId}/users/${identity.identifier}/${identity.id}?flag=${projectFlag.name}`,
                                                        '_blank',
                                                      )
                                                    }}
                                                  >
                                                    {identity.identifier}
                                                  </Flex>
                                                  {canManageOverrides && (
                                                    <Switch
                                                      checked={enabled}
                                                      onChange={() =>
                                                        this.toggleUserFlag({
                                                          enabled,
                                                          id,
                                                          identity,
                                                        })
                                                      }
                                                    />
                                                  )}
                                                  <div className='ml-2'>
                                                    {feature_state_value && (
                                                      <FeatureValue
                                                        value={
                                                          feature_state_value
                                                        }
                                                      />
                                                    )}
                                                  </div>
                                                  <a
                                                    target='_blank'
                                                    href={`/#/project/${this.props.projectId}/environment/${this.props.environmentId}/users/${identity.identifier}/${identity.id}?flag=${projectFlag.name}`}
                                                    className='ml-2 btn btn-link btn--link'
                                                    onClick={() => {}}
                                                    rel='noreferrer'
                                                  >
                                                    Edit
                                                  </a>
                                                </Row>
                                              )}
                                              renderNoResults={
                                                <Panel
                                                  id='users-list'
                                                  title={
                                                    <Tooltip
                                                      title={
                                                        <h6 className='mb-0'>
                                                          Identity Overrides{' '}
                                                          <span className='icon ion-ios-information-circle' />
                                                        </h6>
                                                      }
                                                      place='right'
                                                    >
                                                      {
                                                        Constants.strings
                                                          .IDENTITY_OVERRIDES_DESCRIPTION
                                                      }
                                                    </Tooltip>
                                                  }
                                                >
                                                  <div className='mt-2'>
                                                    No identities are overriding
                                                    this feature.
                                                  </div>
                                                </Panel>
                                              }
                                              isLoading={
                                                !this.state.userOverrides
                                              }
                                            />
                                          </FormGroup>
                                        )}
                                      </Permission>
                                    </TabItem>
                                  )}
                              </Tabs>
                            ) : (
                              <div
                                className={classNames(
                                  !isEdit ? 'create-feature-tab' : '',
                                )}
                              >
                                {Value(
                                  error,
                                  projectAdmin,
                                  createFeature,
                                  project.prevent_flag_defaults,
                                )}
                                {!identity && (
                                  <div className='text-right mr-3'>
                                    <Button
                                      onClick={onCreateFeature}
                                      data-test='create-feature-btn'
                                      id='create-feature-btn'
                                      disabled={
                                        isSaving ||
                                        !name ||
                                        isValuesValid ||
                                        !regexValid
                                      }
                                    >
                                      {isSaving ? 'Creating' : 'Create Feature'}
                                    </Button>
                                  </div>
                                )}
                              </div>
                            )}

                            {identity && (
                              <div className='pr-3'>
                                {identity ? (
                                  <div className='mb-3'>
                                    <p className='text-left ml-3'>
                                      This will update the feature value for the
                                      device <strong>{identityName}</strong>.
                                      {
                                        ' Any segment overrides for this feature will now be ignored.'
                                      }
                                    </p>
                                  </div>
                                ) : (
                                  ''
                                )}

                                <div className='text-right mb-2'>
                                  {identity && (
                                    <div>
                                      <Button
                                        onClick={() => saveFeatureValue()}
                                        data-test='update-feature-btn'
                                        id='update-feature-btn'
                                        disabled={
                                          isSaving || !name || isValuesValid
                                        }
                                      >
                                        {isSaving
                                          ? 'Updating'
                                          : 'Update Feature'}
                                      </Button>
                                    </div>
                                  )}
                                </div>
                              </div>
                            )}
                          </div>
                        )
                      }}
                    </Permission>
                  )}
                </Permission>
              )
            }}
          </Provider>
        )}
      </ProjectProvider>
    )
  }
}

CreateFlag.propTypes = {
  projectId: propTypes.string,
  projectFlag: propTypes.shape({
    id: propTypes.string.isRequired,
  }).isRequired,
  environmentId: propTypes.string,
}

module.exports = ConfigProvider(withSegmentOverrides(CreateFlag))
