import React, { Component } from 'react'
import CreateFlagModal from 'components/modals/CreateFlag'
import TryIt from 'components/TryIt'
import TagFilter from 'components/tags/TagFilter'
import Tag from 'components/tags/Tag'
import FeatureRow from 'components/FeatureRow'
import FeatureListStore from 'common/stores/feature-list-store'
import ProjectStore from 'common/stores/project-store'
import Permission from 'common/providers/Permission'
import { getTags } from 'common/services/useTag'
import { getStore } from 'common/store'
import JSONReference from 'components/JSONReference'
import ConfigProvider from 'common/providers/ConfigProvider'
import Constants from 'common/constants'
import AppActions from '../../../common/dispatcher/app-actions'
import { FormGroup } from '@material-ui/core'

const FeaturesPage = class extends Component {
  static displayName = 'FeaturesPage'

  static contextTypes = {
    router: propTypes.object.isRequired,
  }

  constructor(props, context) {
    super(props, context)
    this.state = {
      search: null,
      showArchived: false,
      sort: { label: 'Name', sortBy: 'name', sortOrder: 'asc' },
      tags: [],
      projectName: ""
    }
    AsyncStorage.getItem('lastEnv').then((res) => {
      if (res) {
        const lastEnv = JSON.parse(res)
        this.setState({
          projectName: lastEnv.projectName
        });
      }
    });
    ES6Component(this)
    this.listenTo(FeatureListStore, 'change', () => this.forceUpdate())
    getTags(getStore(), {
      projectId: `${this.props.match.params.projectId}`,
    })
    AppActions.getFeatures(
      this.props.match.params.projectId,
      this.props.match.params.environmentId,
      true,
      this.state.search,
      this.state.sort,
      0,
      this.getFilter(),
    )
  }

  componentDidUpdate(prevProps, prevState) {
    const {
      match: { params },
    } = this.props
    const {
      match: { params: oldParams },
    } = prevProps
    if (
      params.environmentId !== oldParams.environmentId ||
      params.projectId !== oldParams.projectId
    ) {
      AppActions.getFeatures(
        params.projectId,
        params.environmentId,
        true,
        this.state.search,
        this.state.sort,
        0,
        this.getFilter(),
      )
    }
  }

  componentDidMount = () => {
    API.trackPage(Constants.pages.FEATURES)
    const {
      match: { params },
    } = this.props
  }

  newFlag = () => {
    openModal(
      'New Feature',
      <CreateFlagModal
        router={this.context.router}
        environmentId={this.props.match.params.environmentId}
        projectId={this.props.match.params.projectId}
      />,
      null,
      { className: 'side-modal fade create-feature-modal' },
    )
  }

  getFilter = () => ({
    is_archived: this.state.showArchived,
    tags:
      !this.state.tags || !this.state.tags.length
        ? undefined
        : this.state.tags.join(',')
  })

  onSave = () => {
    toast('Saved')
  }

  onError = (error) => {
    // Kick user back out to projects
    this.setState({ error })
    if (!error?.name && !error?.initial_value) {
      // Could not determine field level error, show generic toast.
      toast(
        'We could not create this feature, please check the name is not in use.',
      )
    }
  }

  filter = () => {
    AppActions.searchFeatures(
      this.props.match.params.projectId,
      this.props.match.params.environmentId,
      true,
      this.state.search,
      this.state.sort,
      0,
      this.getFilter(),
    )
  }

  createFeaturePermission(el) {
    return (
      <Permission
        level='project'
        permission='CREATE_FEATURE'
        id={this.props.match.params.projectId}
      >
        {({ permission }) =>
          permission
            ? el(permission)
            : Utils.renderWithPermission(
                permission,
                Constants.projectPermissions('Write'),
                el(permission),
              )
        }
      </Permission>
    )
  }

  

  render() {
    const { environmentId, projectId } = this.props.match.params
    const readOnly = Utils.getFlagsmithHasFeature('read_only_mode')
    const environment = ProjectStore.getEnvironment(environmentId)
    return (
      <div
        data-test='features-page'
        id='features-page'
        className='app-container container'
      >
        <FeatureListProvider onSave={this.onSave} onError={this.onError}>
          {({ environmentFlags, projectFlags }, { removeFlag, toggleFlag }) => {
            const isLoading = FeatureListStore.isLoading
            return (
              <div className='features-page'>
                {isLoading && (!projectFlags || !projectFlags.length) && (
                  <div className='centered-container'>
                    <Loader />
                  </div>
                )}
                {(!isLoading || (projectFlags)) && (
                  <div>
                    {(projectFlags) ||
                    ((this.state.showArchived ||
                      typeof this.state.search === 'string' ||
                      !!this.state.tags.length) &&
                      !isLoading) ? (
                      <div>
                        <Row>
                          <Flex>
                            <h3>Features</h3>
                            <p>
                              View and manage{' '}
                              <Tooltip
                                title={<ButtonLink>feature flags</ButtonLink>}
                                place='right'
                              >
                                {Constants.strings.FEATURE_FLAG_DESCRIPTION}
                              </Tooltip>{' '}
                              of your {environment && "project_name" in environment ? environment.project_name : ""} bundle.
                            </p>
                          </Flex>
                          <FormGroup className='float-right'>
                            {projectFlags
                              ? this.createFeaturePermission((perm) => (
                                  <div className='text-right'>
                                    <Button
                                      disabled={!perm || readOnly}
                                      data-test='show-create-feature-btn'
                                      id='show-create-feature-btn'
                                      onClick={this.newFlag}
                                    >
                                      Create Feature
                                    </Button>
                                  </div>
                                ))
                              : null}
                          </FormGroup>
                        </Row>
                        <Permission
                          level='environment'
                          permission={Utils.getManageFeaturePermission(
                            Utils.changeRequestsEnabled(
                              environment &&
                                environment.minimum_change_request_approvals,
                            ),
                          )}
                          id={this.props.match.params.environmentId}
                        >
                          {({ permission }) => (
                            <FormGroup className='mb-4'>
                              <PanelSearch
                                className='no-pad'
                                id='features-list'
                                icon='ion-ios-rocket'
                                title='Features'
                                renderSearchWithNoResults
                                itemHeight={65}
                                isLoading={FeatureListStore.isLoading}
                                paging={FeatureListStore.paging}
                                search={this.state.search}
                                onChange={(e) => {
                                  this.setState(
                                    { search: Utils.safeParseEventValue(e) },
                                    () => {
                                      AppActions.searchFeatures(
                                        this.props.match.params.projectId,
                                        this.props.match.params.environmentId,
                                        true,
                                        this.state.search,
                                        this.state.sort,
                                        0,
                                        this.getFilter(),
                                      )
                                    },
                                  )
                                }}
                                nextPage={() =>
                                  AppActions.getFeatures(
                                    this.props.match.params.projectId,
                                    this.props.match.params.environmentId,
                                    true,
                                    this.state.search,
                                    this.state.sort,
                                    FeatureListStore.paging.next,
                                    this.getFilter(),
                                  )
                                }
                                prevPage={() =>
                                  AppActions.getFeatures(
                                    this.props.match.params.projectId,
                                    this.props.match.params.environmentId,
                                    true,
                                    this.state.search,
                                    this.state.sort,
                                    FeatureListStore.paging.previous,
                                    this.getFilter(),
                                  )
                                }
                                goToPage={(page) =>
                                  AppActions.getFeatures(
                                    this.props.match.params.projectId,
                                    this.props.match.params.environmentId,
                                    true,
                                    this.state.search,
                                    this.state.sort,
                                    page,
                                    this.getFilter(),
                                  )
                                }
                                onSortChange={(sort) => {
                                  this.setState({ sort }, () => {
                                    AppActions.getFeatures(
                                      this.props.match.params.projectId,
                                      this.props.match.params.environmentId,
                                      true,
                                      this.state.search,
                                      this.state.sort,
                                      0,
                                      this.getFilter(),
                                    )
                                  })
                                }}
                                sorting={[
                                  {
                                    default: true,
                                    label: 'Name',
                                    order: 'asc',
                                    value: 'name',
                                  },
                                  {
                                    label: 'Created Date',
                                    order: 'asc',
                                    value: 'created_date',
                                  },
                                ]}
                                items={projectFlags}
                                header={
                                  <></>
                                }
                                renderFooter={() => (
                                  <>
                                    <JSONReference
                                      className='mx-2 mt-4'
                                      showNamesButton
                                      title={'Features'}
                                      json={projectFlags}
                                    />
                                    <JSONReference
                                      className='mx-2'
                                      title={'Feature States'}
                                      json={
                                        environmentFlags &&
                                        Object.values(environmentFlags)
                                      }
                                    />
                                  </>
                                )}
                                renderRow={(projectFlag, i) => (
                                  <FeatureRow
                                    environmentFlags={environmentFlags}
                                    projectFlags={projectFlags}
                                    permission={permission}
                                    environmentId={environmentId}
                                    projectId={projectId}
                                    index={i}
                                    canDelete={permission}
                                    toggleFlag={toggleFlag}
                                    removeFlag={removeFlag}
                                    projectFlag={projectFlag}
                                  />
                                )}
                                filterRow={() => true}
                              />
                            </FormGroup>
                          )}
                        </Permission>
                      </div>
                    ) : (
                      <div>
                        {this.createFeaturePermission((perm) => (
                          <FormGroup className='text-center'>
                            <Button
                              disabled={!perm}
                              className='btn-lg btn-primary'
                              id='show-create-feature-btn'
                              data-test='show-create-feature-btn'
                              onClick={this.newFlag}
                            >
                              <span className='icon ion-ios-rocket' /> Create
                              your first Feature
                            </Button>
                          </FormGroup>
                        ))}
                      </div>
                    )}
                  </div>
                )}
              </div>
            )
          }}
        </FeatureListProvider>
      </div>
    )
  }
}

FeaturesPage.propTypes = {}

module.exports = ConfigProvider(FeaturesPage)
