// import propTypes from 'prop-types';
import React, { PureComponent } from 'react'
import classNames from 'classnames'
import ValueEditor from './ValueEditor'
import Constants from 'common/constants'
import VariationOptions from './mv/VariationOptions'
import AddVariationButton from './mv/AddVariationButton'
import ErrorMessage from './ErrorMessage'

const widths = [100, 130, 180, 130]

export default class Feature extends PureComponent {
  static displayName = 'Feature'

  constructor(props) {
    super(props)
    this.state = {
      historyModalOpen: false,
      search: '',
      valid: true,
    }
  }

  removeVariation = (i, id) => {
    const idToRemove = this.props.multivariate_options[i].id

    if (idToRemove) {
      openConfirm(
        'Please confirm',
        'This will remove the variation on your feature for all environments, if you wish to turn it off just for this environment you can set the % value to 0.',
        () => {
          this.props.removeVariation(i, id)
        },
      )
    } else {
      this.props.removeVariation(i, id)
    }
  }

  render() {
    const {
      archieved_multivariate_options,
      checked,
      duplicateVariation,
      environmentFlag,
      environmentVariations,
      error,
      hide_from_client,
      identity,
      isEdit,
      multivariate_options,
      multivariateErrors,
      onCheckedChange,
      onValueChange,
      projectFlag,
      readOnly,
      value,
    } = this.props

    const enabledString = isEdit ? 'Enabled' : 'Enabled by default'
    const disabled = hide_from_client
    const controlValue = Utils.calculateControl(multivariate_options)
    const valueString = identity
      ? 'User override'
      : !!multivariate_options && multivariate_options.length
      ? `Control Value - ${controlValue}%`
      : 'Value *'

    const showValue = !(
      !!identity &&
      multivariate_options &&
      !!multivariate_options.length
    )
    return (
      <div>
        <FormGroup className='mb-4 mr-3 ml-3'>
          <div>
            <label>{enabledString || 'Enabled'}</label>
          </div>
          <Switch
            data-test='toggle-feature-button'
            defaultChecked={checked}
            disabled={disabled || readOnly}
            checked={!disabled && checked}
            onChange={onCheckedChange}
          />
        </FormGroup>

        {showValue && (
          <FormGroup className='mx-3 mb-4 mr-3'>
            <InputGroup
              component={
                <ValueEditor
                  data-test='featureValue'
                  name='featureValue'
                  className={classNames({
                    'full-width': true,
                    'variation-value-invalid': !this.state.valid,
                  })}
                  value={`${
                    typeof value === 'undefined' || value === null ? '' : value
                  }`}
                  onChange={(e) => {
                    !identity &&
                      !!multivariate_options &&
                      multivariate_options.length &&
                      this.setState({
                        valid: Utils.safeParseEventValue(e)?.length,
                      })
                    onValueChange(e)
                  }}
                  disabled={
                    hide_from_client ||
                    readOnly ||
                    (!!multivariate_options.length && isEdit)
                  }
                  placeholder="e.g. 'big' "
                />
              }
              tooltip={Constants.strings.REMOTE_CONFIG_DESCRIPTION}
              title={`${valueString}`}
            />
          </FormGroup>
        )}

        {!!error && (
          <div className='mx-2 mt-2'>
            <ErrorMessage error={error} />
          </div>
        )}

        {multivariateErrors.map((error, index) => (
          <div className='mx-3 mt-2' key={index}>
            <div className='alert alert-danger'>{error}</div>
          </div>
        ))}

        {!!identity && (
          <div>
            <FormGroup className='mb-4 mx-3'>
              <VariationOptions
                disabled
                select
                controlValue={environmentFlag.feature_state_value}
                variationOverrides={this.props.identityVariations}
                setVariations={this.props.onChangeIdentityVariations}
                updateVariation={() => {}}
                weightTitle='Override Weight %'
                projectFlag={projectFlag}
                multivariateOptions={projectFlag.multivariate_options}
                removeVariation={() => {}}
                duplicateVariation={() => {}}
                isEdit={isEdit}
              />
            </FormGroup>
          </div>
        )}
        {!identity && (
          <div>
            <FormGroup className='ml-3 mb-4 mr-3'>
              {(!!environmentVariations || !isEdit) && (
                <VariationOptions
                  isEdit={isEdit}
                  disabled={!!identity || readOnly}
                  controlValue={controlValue}
                  variationOverrides={environmentVariations}
                  updateVariation={this.props.updateVariation}
                  weightTitle={isEdit ? 'Weight %' : 'Default Weight %'}
                  multivariateOptions={multivariate_options}
                  duplicateVariation={duplicateVariation}
                  removeVariation={this.removeVariation}
                />
              )}
            </FormGroup>
            {isEdit && !identity && (
              <>
                <div className='ml-3 mb-2'>
                  <Button
                    disabled={archieved_multivariate_options.length === 0}
                    data-toggle='collapse'
                    data-target='#collapseExample'
                    aria-expanded='false'
                    aria-controls='collapseExample'
                  >
                    Show Archived
                  </Button>
                </div>
                <div className='collapse mx-3' id='collapseExample'>
                  <div className='card card-body'>
                    <PanelSearch
                      className='no-pad'
                      id='feature-variation-archived-list'
                      title='Variation archived list'
                      itemHeight={65}
                      search={null}
                      items={archieved_multivariate_options}
                      header={
                        <Row className='table-header archived-list-grid'>
                          <div className='text-center'>ID</div>
                          <div className='text-center'>Name</div>
                          <Flex className='text-center'>Value</Flex>
                          <Flex className='text-right' />
                        </Row>
                      }
                      renderRow={(historyItem, i) => (
                        <Row className='py-2 px-3 archived-list-grid '>
                          <span className='text-center'>
                            {historyItem.custom_id}
                          </span>
                          <div className='text-center ml-1'>
                            {historyItem.name}
                          </div>
                          <span className='text-center'>
                            <FeatureValue
                              value={Utils.featureStateToValue(historyItem)}
                              data-test={`feature-value-${this.props.index}`}
                            />
                          </span>
                          <div className='flex ml-auto'>
                            <Button
                              onClick={() =>
                                this.props.restoreVariation(historyItem, i)
                              }
                            >
                              Restore
                            </Button>
                          </div>
                        </Row>
                      )}
                      filterRow={() => true}
                    />
                  </div>
                </div>
              </>
            )}
            {!this.props.hideAddVariation &&
              Utils.renderWithPermission(
                this.props.canCreateFeature,
                Constants.projectPermissions('Create Feature'),
                <AddVariationButton
                  disabled={!this.props.canCreateFeature || readOnly}
                  onClick={() => this.props.addVariation()}
                />,
              )}
          </div>
        )}
      </div>
    )
  }
}
