import React, { Component } from 'react'
import { FixedSizeList as List } from 'react-window'
import Popover from './base/Popover'

const PanelSearch = class extends Component {
  static displayName = 'PanelSearch'

  static propTypes = {
    action: OptionalNode,
    filterRow: OptionalFunc,
    goToPage: OptionalFunc,
    isLoading: OptionalBool,
    items: propTypes.any,
    nextPage: OptionalFunc,
    noResultsText: OptionalString,
    paging: OptionalObject,
    renderNoResults: propTypes.any,
    renderRow: RequiredFunc,
    search: OptionalString,
    searchPanel: OptionalNode,
    sorting: OptionalArray,
    title: propTypes.node,
  }

  constructor(props, context) {
    super(props, context)
    const defaultSortingOption = _.find(_.get(props, 'sorting', []), {
      default: true,
    })
    this.state = {
      sortBy: defaultSortingOption ? defaultSortingOption.value : null,
      sortOrder: defaultSortingOption ? defaultSortingOption.order : null,
    }
  }

  filter() {
    let search = this.props.search || this.state.search || ''
    if (this.state.exact) {
      while (search.startsWith('"')) {
        search = search.slice(1);
      }
      while (search.endsWith('"')) {
        search = search.slice(0, -1);
      }
    }
    const filter = this.props.filter
    const { filterRow, items } = this.props
    if (filterRow && (search || filter)) {
      return this.sort(
        _.filter(items, (value, index) =>
          filterRow(value, search.toLowerCase(), index),
        ),
      )
    }
    return this.sort(items)
  }

  sort(items) {
    const { sortBy, sortOrder } = this.state
    if (sortBy) {
      return _.orderBy(items, [sortBy], [sortOrder])
    }

    return items
  }

  onSort(e, sortOption) {
    e.preventDefault()
    const { sortBy, sortOrder } = this.state
    if (sortOption.value === sortBy) {
      this.setState({ sortOrder: sortOrder === 'asc' ? 'desc' : 'asc' }, () => {
        if (this.props.onSortChange) {
          this.props.onSortChange({
            sortBy: this.state.sortBy,
            sortOrder: this.state.sortOrder,
          })
        }
      })
    } else {
      this.setState(
        { sortBy: sortOption.value, sortOrder: sortOption.order },
        () => {
          if (this.props.onSortChange) {
            this.props.onSortChange({
              sortBy: this.state.sortBy,
              sortOrder: this.state.sortOrder,
            })
          }
        },
      )
    }
  }

  renderContainer = (children) => {
    const renderRow = ({ index, style }) => (
      <div style={style}>{this.props.renderRow(children[index])}</div>
    )
    if (children && children.length > 100 && this.props.itemHeight) {
      return (
        <List
          style={{ overflowX: 'hidden' }}
          height={this.props.itemHeight * 10}
          itemCount={children.length}
          itemSize={this.props.itemHeight}
          width='100%'
        >
          {renderRow}
        </List>
      )
    }
    return children.map(this.props.renderRow)
  }

  renderSortOptions(toggle, currentSort, sortOrder) {
    return (
        <div className='popover-inner__content'>
            {this.props.sorting.map((sortOption, i) => (
                <a
                    key={i}
                    className='popover-bt__list-item'
                    href='#'
                    onClick={(e) => {
                        this.onSort(e, sortOption)
                        toggle()
                    }}
                >
                    <Row space>
                        <Row className='flex-1'>{sortOption.label}</Row>
                        {currentSort && currentSort.value === sortOption.value && (
                            <Row>
                                <div
                                    className={`flex-column ion ${
                                        sortOrder === 'asc'
                                            ? 'ion-ios-arrow-up'
                                            : 'ion-ios-arrow-down'
                                    }`}
                                />
                            </Row>
                        )}
                    </Row>
                </a>
            ))}
        </div>
    )
}

renderSearchInput(placeholder, search) {
    return (
        <Row onClick={() => this.input.focus()}>
            <input
                ref={(c) => (this.input = c)}
                onBlur={this.props.onBlur}
                onChange={(e) => {
                    const v = Utils.safeParseEventValue(e)
                    this.props.onChange
                        ? this.props.onChange(this.state.exact ? `"${v}"` : v)
                        : this.setState({
                            search: Utils.safeParseEventValue(e),
                        })
                }}
                style={{ padding: '0.5rem' }}
                type='text'
                id={`${this.props.id}-search`}
                data-test={`${this.props.id}-search`}
                value={search}
                className='pl-4'
                placeholder={placeholder}
            />
            <span
                style={{ marginLeft: 10, position: 'absolute' }}
                className='icon ion-ios-search'
            />
        </Row>
    )
}

renderFilterRow(currentSort, sortOrder, search, placeholder) {
    return (
        <Row>
            {!!this.props.sorting && (
                <Row className='mr-3 relative'>
                    <Popover
                        renderTitle={(toggle) => (
                            <a onClick={toggle}>
                                <div className='flex-column ion ion-md-funnel' />
                                {currentSort ? currentSort.label : 'Unsorted'}
                            </a>
                        )}
                    >
                        {(toggle) => this.renderSortOptions(toggle, currentSort, sortOrder)}
                    </Popover>
                </Row>
            )}
            {!!this.props.filterRow && (
                <>
                    {this.renderExactFilter()}
                    {this.renderSearchInput(placeholder, search)}
                </>
            )}
        </Row>
    )
}

renderExactFilter() {
    return (
        this.props.showExactFilter && (
            <div className='mr-2' style={{ width: 200 }}>
                <Select
                    className='select-sm'
                    styles={{
                        control: (base) => ({
                            ...base,
                            '&:hover': { borderColor: '$bt-brand-secondary' },
                            border: '1px solid $bt-brand-secondary',
                            height: 30,
                        }),
                    }}
                    onChange={(v) => {
                        this.setState({ exact: v.label === 'Exact' })
                        if (this.props.search) {
                            let trimmedSearch = this.props.search;
                            if (this.state.exact) {
                              while (trimmedSearch.startsWith('"')) {
                                trimmedSearch = trimmedSearch.slice(1);
                              }
                              while (trimmedSearch.endsWith('"')) {
                                trimmedSearch = trimmedSearch.slice(0, -1);
                              }
                            }
                            this.props.onChange &&
                                this.props.onChange(
                                    !this.state.exact
                                        ? `"${this.props.search}"`
                                        : trimmedSearch,
                                )
                        }
                    }}
                    value={{
                        label: this.state.exact
                            ? 'Exact'
                            : this.props.filterLabel ||
                            (Utils.getIsEdge() ? 'Starts with' : 'Contains'),
                    }}
                    options={[
                        {
                            label: Utils.getIsEdge()
                                ? 'Starts with'
                                : 'Contains',
                            value: 'Contains',
                        },
                        {
                            label: 'Exact',
                            value: 'Exact',

                        },
                    ]}
                />
            </div>
        )
    )
}


renderContent(filteredItems, isLoading, items, search, renderNoResults) {
    if (isLoading && (!filteredItems || !items)) {
        return (
            <div className='text-center'>
                <Loader />
            </div>
        )
    }

    if (filteredItems && filteredItems.length) {
        return this.renderContainer(filteredItems)
    }

    if (renderNoResults && !search) {
        return renderNoResults
    }

    return (
        <Column>
            {!isLoading && (
                <div className='mx-2 pt-1 pb-2'>
                    {this.props.noResultsText?.(search) || (
                        <>
                            {'No results '}
                            {search && (
                                <span>
                                    for
                                    <strong>{` "${search}"`}</strong>
                                </span>
                            )}
                        </>
                    )}
                </div>
            )}
        </Column>
    )
}

  render() {
    const { sortBy, sortOrder } = this.state
    const {
        goToPage,
        isLoading,
        items,
        nextPage,
        paging,
        prevPage,
        renderNoResults,
        sorting,
    } = this.props
    const filteredItems = this.filter(items)
    const currentSort = _.find(sorting, { value: sortBy })

    let search = this.props.search || this.state.search || ''
    if (this.state.exact) {
      while (search.startsWith('"')) {
        search = search.slice(1);
      }
      while (search.endsWith('"')) {
        search = search.slice(0, -1);
      }
    }

    const placeholders = {
        'features-list': 'Search for features',
        'segment-list': 'Search for segments',
        'user-features-list': 'Search for features',
        'user-segments-list': 'Search for segments',
        'user-traits-list': 'Search for traits',
        'users-list': 'Search with device id',
    }
    const placeholder = placeholders[this.props.id] || 'Search'

    return !search &&
        (!filteredItems || !filteredItems.length) &&
        !this.props.isLoading &&
        !this.props.renderSearchWithNoResults ? (
        renderNoResults || null
    ) : (
        <Panel
            className={this.props.className}
            title={this.props.title}
            icon={this.props.icon}
            renderFooter={this.props.renderFooter}
            action={
                this.props.filterRow || this.props.sorting
                    ? this.renderFilterRow(currentSort, sortOrder, search, placeholder)
                    : this.props.action || null
            }
        >
            {this.props.searchPanel}
            <div
                id={this.props.id}
                className='search-list'
                style={isLoading ? { opacity: 0.5 } : {}}
            >
                {!!paging && (
                    <Paging
                        paging={paging}
                        isLoading={isLoading}
                        nextPage={nextPage}
                        prevPage={prevPage}
                        goToPage={goToPage}
                    />
                )}
                {this.props.header}
                {this.renderContent(filteredItems, isLoading, items, search, renderNoResults)}
            </div>
            {!!paging && filteredItems && filteredItems.length > 10 && (
                <Paging paging={paging} isLoading={isLoading} goToPage={goToPage} />
            )}
        </Panel>
    )
  }

}
export default PanelSearch