  GUID(append) {
    let d = new Date().getTime()
    const uuid = 'xxxx-xxxx-xxxx'.replace(/[xy]/g, (c) => {
      const r = (d + crypto.getRandomValues(new Uint8Array(1))[0] % 16) % 16;
      d = Math.floor(d / 16);
      return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });

    return append ? `${uuid}-${append}` : uuid
  },

