import React, { FC, FormEvent, useEffect, useMemo, useState } from 'react'
import AppLoader from 'components/AppLoader'

import Constants from 'common/constants'
import useSearchThrottle from 'common/useSearchThrottle'
import {
  EdgePagedResponse,
  Identity,
  Segment,
  SegmentRule,
} from 'common/types/responses'
import { Req } from 'common/types/requests'
import { useGetIdentitiesQuery } from 'common/services/useIdentity'
import {
  useGetAllTraitsQuery,
  useCreateSegmentMutation,
  useGetSegmentQuery,
  useUpdateSegmentMutation,
  useGetAffectedIdentitiesCountMutation,
} from 'common/services/useSegment'
import Format from 'common/utils/format'
import Utils from 'common/utils/utils'

import Button, { ButtonLink, ButtonOutline } from 'components/base/forms/Button'
import Input from 'components/base/forms/Input'
import InputGroup from 'components/base/forms/InputGroup'
import Rule from './Rule'
import TabItem from 'components/base/forms/TabItem'
import Tabs from 'components/base/forms/Tabs'
import ConfigProvider from 'common/providers/ConfigProvider'
import JSONReference from 'components/JSONReference'
import { cloneDeep, isEqual } from 'lodash'
import { useHasPermission } from 'common/providers/Permission'

type PageType = {
  number: number
  pageType: Req['getIdentities']['pageType']
  pages: Req['getIdentities']['pages']
}

type CreateSegmentType = {
  projectId: number | string
  searchInput: string
  environmentId: string
  identitiesLoading: boolean
  setEnvironmentId: (env: string) => void
  setSearchInput: (search: string) => void
  page: PageType
  setPage: (page: PageType) => void
  feature?: number
  identities?: EdgePagedResponse<Identity>
  identity?: boolean
  condensed?: boolean
  isEdit?: boolean
  onCancel?: () => void
  onComplete?: (segment: Segment) => void
  readOnly?: boolean
  segment?: Segment
}

const CreateSegment: FC<CreateSegmentType> = ({
  condensed,
  environmentId,
  feature,
  identities,
  identitiesLoading,
  identity,
  isEdit,
  onCancel,
  onComplete,
  page,
  projectId,
  readOnly,
  searchInput,
  segment: _segment,
  setEnvironmentId,
  setPage,
  setSearchInput
}) => {
  const SEGMENT_ID_MAXLENGTH = Constants.forms.maxLength.SEGMENT_ID

  const [traitObj, setTraitObj] = useState<{ label: string; value: string }[]>([]);

  // Use the useGetAllTraitsQuery hook to get the query result and refetch function
  const { data, isLoading, refetch } = useGetAllTraitsQuery({ environmentId });

  // Manually trigger the query on mount and when the environmentId changes
  useEffect(() => {
    refetch();
  }, [environmentId]);

  useEffect(() => {
    if (!isLoading && data) {
      const traitObject = data?.keys.map(key => {
          return {
              label: key,
              value: key
          };
      });
      setTraitObj(traitObject);
    }
  }, [data, isLoading]);

  const defaultSegment: Omit<Segment, 'id' | 'uuid' | 'project'> & {
    id?: number
    uuid?: string
    project?: number
  } = {
    description: '',
    name: '',
    rules: [
      {
        conditions: [],
        rules: [
          {
            conditions: [{ ...Constants.defaultRule }],
            rules: [],
            type: 'ANY',
          },
        ],
        type: 'ALL',
      },
    ],
  }
  const segment = _segment || defaultSegment
  const [
    createSegment,
    {
      data: createSegmentData,
      isError: createError,
      isLoading: creating,
      isSuccess: createSuccess,
    },
  ] = useCreateSegmentMutation()
  const [
    editSegment,
    {
      data: updateSegmentData,
      isError: updateError,
      isLoading: updating,
      isSuccess: updateSuccess,
    },
  ] = useUpdateSegmentMutation()

  const [
    getAffectedIdentities,
    {
      data: identitiesCountData,
      isError: getIdentitiesCountError,
      isLoading: isFetchingCount,
      isSuccess: fetchCountSuccess,
    },
  ] = useGetAffectedIdentitiesCountMutation()

  const isSaving = creating || updating
  const [showDescriptions, setShowDescriptions] = useState(false)
  const [description, setDescription] = useState(segment.description)
  const [name, setName] = useState<Segment['name']>(segment.name)
  const [rules, setRules] = useState<Segment['rules']>(segment.rules)
  const [tab, setTab] = useState(0)
  const [lastIdentitiesRules, setLastIdentitiesRules] = useState<Segment['rules']>([])
  const [enableGetIdentities, setEnableGetIdentities] = useState(false)

  const isError = createError || updateError

  const { permission: writeSegmentsPermission } = useHasPermission({
    id: String(projectId),
    level: 'project',
    permission: 'MANAGE_SEGMENTS',
  })

  const { permission: deleteRulePermission } = useHasPermission({
    id: String(projectId),
    level: 'project',
    permission: 'MANAGE_SEGMENTS',
  })

  const filterConditions = (rules: any) => {
    return rules.map((rule: { conditions: any[]; rules: any }) => ({
      ...rule,
      conditions: rule.conditions.filter(condition => condition.delete !== "true"),
      rules: filterConditions(rule.rules),
    }));
  }

  const addRule = (type = 'ANY') => {
    const newRules = cloneDeep(rules)
    newRules[0].rules = newRules[0].rules.concat({
      conditions: [{ ...Constants.defaultRule }],
      rules: [],
      type,
    })
    setRules(newRules)
  }

  const updateRule = (
    rulesIndex: number,
    elementNumber: number,
    newValue: SegmentRule,
  ) => {
    const newRules = cloneDeep(rules)
    newRules[0].rules[elementNumber] = newValue
    setRules(newRules)
  }

  useEffect(() => {
    validateAffectedIdentities()
  }, [rules])

  const removeRule = (rulesIndex: number, elementNumber: number) => {
    const newRules = cloneDeep(rules)
    newRules[0].rules.splice(elementNumber, 1)
    setRules(newRules)
  }

  const getAffectedIdentitiesCount = () => {
    getAffectedIdentities({
      projectId: '',
      segment: {
        rules
      }
    })
    validateAffectedIdentities()
  }

  const validateAffectedIdentities = () => {
    const output = filterConditions(rules);
    if (isValid && !lastIdentitiesRules.length) {
      setLastIdentitiesRules(output)
      setEnableGetIdentities(true)
    } else {
        setLastIdentitiesRules(output)
        const isIdentical: boolean = isEqual(lastIdentitiesRules, output)
        if (!isIdentical) {
          setEnableGetIdentities(true)
        } else {
          setEnableGetIdentities(false)
        }
    }
  }

  const save = (e: FormEvent) => {
    Utils.preventDefault(e)
    const segmentData: Omit<Segment, 'id' | 'uuid'> = {
      description,
      feature: feature,
      name,
      project: projectId,
      rules,
    }
    if (name) {
      if (segment.id) {
        editSegment({
          projectId,
          segment: {
            ...segmentData,
            id: segment.id,
            project: segment.project as number,
            uuid: segment.uuid as string,
          },
        })
      } else {
        createSegment({
          projectId,
          segment: segmentData,
        })
      }
    }
  }

  const isValid = useMemo(() => {
    if (!rules[0]?.rules?.find((v) => !v.delete)) {
      return false
    }
    const res = rules[0].rules.find((v) =>
      v.conditions.find((c) => !Utils.validateRule(c)),
    )
    return !res
  }, [rules])

  useEffect(() => {
    setTimeout(() => {
      document.getElementById('segmentID')?.focus()
    }, 500)
  }, [])
  useEffect(() => {
    if (createSuccess && createSegmentData) {
      onComplete?.(createSegmentData)
    }
    //eslint-disable-next-line
  }, [createSuccess])
  useEffect(() => {
    if (updateSuccess && updateSegmentData) {
      onComplete?.(updateSegmentData)
    }
    //eslint-disable-next-line
  }, [updateSuccess])

  const rulesEl = (
    <div className='overflow-visible'>
      <div>
        <div className='mb-2'>
          {rules[0].rules.map((rule, i) => {
            if (rule.delete) {
              return null
            }
            return (
              <div key={i}>
                {i > 0 && (
                  <Row className='and-divider my-1'>
                    <Flex className='and-divider__line' />
                    {rule.type === 'ANY' ? 'AND' : 'AND NOT'}
                    <Flex className='and-divider__line' />
                  </Row>
                )}
                <Rule
                  showDescription={showDescriptions}
                  readOnly={readOnly}
                  data-test={`rule-${i}`}
                  rule={rule}
                  operators={
                    Utils.getFlagsmithValue('segment_operators')
                      ? JSON.parse(Utils.getFlagsmithValue('segment_operators'))
                      : null
                  }
                  onRemove={() => removeRule(0, i)}
                  onChange={(v: SegmentRule) => updateRule(0, i, v)}
                  traits={traitObj}
                  deleteRulePermission={deleteRulePermission}
                  currentIndex={i}
                />
              </div>
            )
          })}
        </div>
        <Row className='justify-content-end mt-3'>
          {
            !getIdentitiesCountError && identitiesCountData && <p className='identities-count-label'>Affected Identites Count: {!getIdentitiesCountError && identitiesCountData ? identitiesCountData?.identity_count : 0}</p>
          }
          <ButtonOutline
            disabled={isFetchingCount || !isValid || !enableGetIdentities || !writeSegmentsPermission}
            type='button'
            data-test='affected-identities'
            onClick={getAffectedIdentitiesCount}
          >
            {isFetchingCount ? 'Fetching Count' : 'Get Affected Identities'}
          </ButtonOutline>
        </Row>
        <Row className='justify-content-center'>
          {!readOnly && (
            <div
              onClick={() => addRule('ANY')}
              style={{ marginTop: 20 }}
              className='text-center'
            >
              <ButtonOutline data-test='add-rule' type='button'>
                Add AND Condition
              </ButtonOutline>
            </div>
          )}
          {!readOnly && Utils.getFlagsmithHasFeature('not_operator') && (
            <div
              onClick={() => addRule('NOT')}
              style={{ marginTop: 20 }}
              className='text-center'
            >
              {Utils.getFlagsmithValue('not_operator') ? (
                <Tooltip
                  title={
                    <ButtonOutline
                      className='ml-2 btn--outline-danger'
                      data-test='add-rule'
                      type='button'
                    >
                      Add AND NOT Condition
                    </ButtonOutline>
                  }
                >
                  {`Note: If using clientside evaluations on your SDK, this feature is only supported by the following SDKs: ${JSON.parse(
                    Utils.getFlagsmithValue('not_operator'),
                  )}`}
                </Tooltip>
              ) : (
                <ButtonOutline
                  className='ml-2 btn--outline-danger'
                  data-test='add-rule'
                  type='button'
                >
                  Add AND NOT Condition
                </ButtonOutline>
              )}
            </div>
          )}
        </Row>
      </div>
    </div>
  )

  const renderSegmentIDField = (
    isEdit: boolean, 
    name: string, 
    setName: (value: string) => void
  ) => (
    !isEdit && (
      <Row className='mb-4'>
        <label className='mr-2 mb-0' htmlFor='segmentID'>
          ID
        </label>
        <Flex>
          <Input
            data-test='segmentID'
            name='id'
            id='segmentID'
            readOnly={isEdit}
            maxLength={SEGMENT_ID_MAXLENGTH}
            value={name}
            onChange={(e: InputEvent) =>
              setName(
                Format.enumeration
                  .set(Utils.safeParseEventValue(e))
                  .toLowerCase(),
              )
            }
            isValid={name && name.length}
            type='text'
            title={isEdit ? 'ID' : 'ID*'}
            placeholder='E.g. power_users'
          />
        </Flex>
      </Row>
    )
  )

  const renderDescriptionField = (
    condensed: boolean, 
    description: string, 
    setDescription: (value: string) => void, 
    identity: any,  // Replace 'any' with the correct type for identity, if available
    readOnly: boolean
  ) => (
    !condensed && (
      <FormGroup className='mb-4'>
        <InputGroup
          value={description}
          inputProps={{
            className: 'full-width',
            name: 'featureDesc',
            readOnly: !!identity || readOnly,
          }}
          onChange={(e: InputEvent) =>
            setDescription(Utils.safeParseEventValue(e))
          }
          isValid={description && description.length}
          type='text'
          title='Description (optional)'
          placeholder="e.g. 'People who have spent over $100' "
        />
      </FormGroup>
    )
  )
  
  const renderRulesSection = (rulesEl: JSX.Element) => (
    <div className='form-group'>
      <Row className='mt-2 mb-2'>
        <Flex>
          <label className='cols-sm-2 control-label'>
            Include users when the following rules apply:
          </label>
          <span className='text-small text-muted'>
            Note: Trait names are case sensitive
          </span>
        </Flex>
      </Row>
      {rulesEl}
    </div>
  )

  const renderErrorAlert = (isError: boolean) => (
    isError && (
      <div className='alert alert-danger'>
        Error creating segment, please ensure you have entered a trait and value for each rule.
      </div>
    )
  )

  const renderActionButtons = (
    isEdit: boolean,
    isSaving: boolean,
    name: string,
    isValid: boolean,
    writeSegmentsPermission: boolean,
    onCancel: () => void,
    condensed: boolean
  ) => (
    <div className='text-right'>
      <Row className='justify-content-end'>
        {condensed && (
          <ButtonLink type='button' onClick={onCancel} className='mr-4'>
            Cancel
          </ButtonLink>
        )}
        {Utils.renderWithPermission(
          writeSegmentsPermission,
          Constants.projectPermissions('Write'),
          <Button
            type='submit'
            data-test={isEdit ? 'update-segment' : 'create-segment'}
            id={isEdit ? 'update-feature-btn' : 'create-feature-btn'}
            disabled={isSaving || !name || !isValid || !writeSegmentsPermission}
          >
            {isSaving ? (isEdit ? 'Updating' : 'Creating') : (isEdit ? 'Update Segment' : 'Create Segment')}
          </Button>
        )}
      </Row>
    </div>
  )

  const renderJSONReference = (isEdit: boolean, segment: Segment) => (
    isEdit && <JSONReference title={'Segment'} json={segment} />
  )

  const Tab1 = (isEdit: any, condensed: any, readOnly: any, segment: any, onCancel: any) => (
    <form id='create-segment-modal' onSubmit={save}>
      {renderSegmentIDField(isEdit, name, setName)}
      {renderDescriptionField(condensed, description, setDescription, identity, readOnly)}
      {renderRulesSection(rulesEl)}
      {renderErrorAlert(isError)}
      {renderJSONReference(isEdit, segment)}
      {renderActionButtons(isEdit, isSaving, name, isValid, writeSegmentsPermission, onCancel, condensed)}
    </form>
  )

  return (
    <div>
      {isLoading ? <AppLoader /> : (
        isEdit && !condensed ? (
          <Tabs value={tab} onChange={(tab: number) => setTab(tab)}>
            <TabItem tabLabel='Rules'>
              <div className='mt-4 mr-3 ml-3'>{Tab1(isEdit, condensed, readOnly, segment, onCancel)}</div>
            </TabItem>
          </Tabs>
        ) : (
          <div className='mt-4 mr-3 ml-3'>{Tab1(isEdit, condensed, readOnly, segment, onCancel)}</div>
        )
      )
      }
    </div>
  )
}

type LoadingCreateSegmentType = {
  condensed?: boolean
  environmentId: string
  isEdit?: boolean
  readOnly?: boolean
  onComplete?: () => void
  projectId: string
  segment?: number
}

const LoadingCreateSegment: FC<LoadingCreateSegmentType> = (props) => {
  const [environmentId, setEnvironmentId] = useState(props.environmentId)
  const { data: segmentData, isLoading } = useGetSegmentQuery(
    {
      id: `${props.segment}`,
      projectId: `${props.projectId}`,
    },
    { skip: !props.segment },
  )

  const [page, setPage] = useState<PageType>({
    number: 1,
    pageType: undefined,
    pages: undefined,
  })

  const { search, searchInput, setSearchInput } = useSearchThrottle(
    Utils.fromParam().search,
    () => {
      setPage({
        number: 1,
        pageType: undefined,
        pages: undefined,
      })
    },
  )

  const isEdge = Utils.getIsEdge()

  const { data: identities, isLoading: identitiesLoading } =
    useGetIdentitiesQuery({
      environmentId,
      isEdge,
      page: page.number,
      pageType: page.pageType,
      page_size: 10,
      pages: page.pages,
      search,
    })

  return isLoading ? (
    <div className='text-center'>
      <Loader />
    </div>
  ) : (
    <CreateSegment
      {...props}
      segment={segmentData || undefined}
      identities={identities}
      setPage={setPage}
      searchInput={searchInput}
      setSearchInput={setSearchInput}
      identitiesLoading={identitiesLoading}
      page={page}
      environmentId={environmentId}
      setEnvironmentId={setEnvironmentId}
    />
  )
}

export default LoadingCreateSegment

module.exports = ConfigProvider(LoadingCreateSegment)
