import React, { Component } from 'react'
import { matchPath } from 'react-router'
import { withRouter } from 'react-router-dom'
import amplitude from 'amplitude-js'
import Aside from './Aside'
import TwoFactorPrompt from './SimpleTwoFactor/prompt'
import Maintenance from './Maintenance'
import Blocked from './Blocked'
import AppLoader from './AppLoader'
import Headway from './Headway'
import ProjectStore from 'common/stores/project-store'
import { Provider } from 'react-redux'
import { getStore } from 'common/store'
import { resolveAuthFlow } from '@datadog/ui-extensions-sdk'
import ConfigProvider from 'common/providers/ConfigProvider'
const App = class extends Component {
  static propTypes = {
    children: propTypes.element.isRequired,
  }

  static contextTypes = {
    router: propTypes.object.isRequired,
  }

  state = {
    asideIsVisible: !isMobile,
    pin: '',
    totalApiCalls: 0,
  }

  constructor(props, context) {
    super(props, context)
    ES6Component(this)
  }

  componentDidMount = () => {
    // getBuildVersion()
    this.listenTo(ProjectStore, 'change', () => this.forceUpdate())
    window.addEventListener('scroll', this.handleScroll)
  }

  toggleDarkMode = () => {
    const newValue = !Utils.getFlagsmithHasFeature('dark_mode')
    flagsmith.setTrait('dark_mode', newValue)
    if (newValue) {
      document.body.classList.add('dark')
    } else {
      document.body.classList.remove('dark')
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.location.pathname !== this.props.location.pathname) {
      if (isMobile) {
        this.setState({ asideIsVisible: false })
      }
      this.hideMobileNav()
    }
  }

  hideMobileNav = () => {
    if (this.mobileNav && this.mobileNav.isActive()) {
      this.mobileNav.toggle()
    }
  }

  toggleAside = () => {
    this.setState({ asideIsVisible: !this.state.asideIsVisible })
  }

  onLogin = () => {
    resolveAuthFlow({
      isAuthenticated: true,
    })

    let redirect = API.getRedirect()
    const invite = API.getInvite()
    if (invite) {
      redirect = `/invite/${invite}`
    }

    const referrer = API.getReferrer()
    let query = ''
    if (referrer) {
      query = `?${Utils.toParam(referrer)}`
    }

    if (AccountStore.ephemeral_token) {
      this.forceUpdate()
      return
    }

    if (!AccountStore.getOrganisation() && !invite) {
      // If user has no organisation redirect to /create
      this.context.router.history.replace(`/create${query}`)
      return
      // this.onLogout();
    }

    // Redirect on login
    if (
      this.props.location.pathname === '/' ||
      this.props.location.pathname === '/widget' ||
      this.props.location.pathname === '/saml' ||
      this.props.location.pathname.includes('/oauth') ||
      this.props.location.pathname === '/login' ||
      this.props.location.pathname === '/signup'
    ) {
      if (redirect) {
        API.setRedirect('')
        this.context.router.history.replace(redirect)
      } else {
        AsyncStorage.getItem('lastEnv').then((res) => {
          if (res) {
            const lastEnv = JSON.parse(res)
            const lastOrg = _.find(AccountStore.getUser().organisations, {
              id: lastEnv.orgId,
            })
            if (!lastOrg) {
              this.context.router.history.replace('/projects')
              // this.onLogout();
              return
            }

            const org = AccountStore.getOrganisation()
            if (!org || org.id !== lastOrg.id) {
              AppActions.selectOrganisation(lastOrg.id)
              AppActions.getOrganisation(lastOrg.id)
            }

            this.context.router.history.replace(
              `/project/${lastEnv.projectId}/environment/${lastEnv.environmentId}/features`,
            )
            return
          }
          // this.onLogout();
          this.context.router.history.replace('/projects')
        })
      }
    }

    if (Utils.getFlagsmithHasFeature('dark_mode')) {
      document.body.classList.add('dark')
    }
  }

  handleScroll = () => {
    if (this.scrollPos < 768 && $(document).scrollTop() >= 768) {
      this.setState({ myClassName: 'scrolled' })
    } else if (this.scrollPos >= 768 && $(document).scrollTop() < 768) {
      this.setState({ myClassName: '' })
    }
    this.scrollPos = $(document).scrollTop()
  }

  onLogout = () => {
    resolveAuthFlow({
      isAuthenticated: false,
    })
    if (document.location.href.includes('saml?')) {
      return
    }
    this.context.router.history.replace('/')
  }

  renderTwoFactorPrompt = (isSaving, twoFactorLogin) => (
    <div className='col-md-6 push-md-3 mt-5'>
      <TwoFactorPrompt
        pin={this.state.pin}
        error={this.state.error}
        onSubmit={() => {
          this.setState({ error: false })
          twoFactorLogin(this.state.pin, () => this.setState({ error: true }))
        }}
        isLoading={isSaving}
        onChange={(e) => this.setState({ pin: Utils.safeParseEventValue(e) })}
      />
    </div>
  )

  renderNavbar = (user, pageHasAside, isMobile, asideIsVisible, logoImgLocation, projectId) => (
    <nav className='navbar'>
      <Row space>
        <div className='navbar-left'>
          <div className='navbar-nav'>
            {pageHasAside && !asideIsVisible && this.renderAsideToggle()}
            {!projectId && this.renderLogo(logoImgLocation)}
          </div>
        </div>
        <Row className='navbar-right' style={{ display: 'none' }}>
          {user ? this.renderUserNav(user) : <div />}
        </Row>
      </Row>
    </nav>
  )

  renderAsideToggle = () => (
    <div role='button' className='clickable toggle' onClick={this.toggleAside}>
      <span className='icon ion-md-menu' />
    </div>
  )

  renderLogo = (logoImgLocation) => (
    <a href='#'>
      <img title='Feature Flag' height={25} src={logoImgLocation} className='brand' alt='Flagsmith logo' />
    </a>
  )

  renderUserNav = (user) => (
    <>
      <nav className='my-2 my-md-0 hidden-xs-down'>
        <Headway className='nav-link cursor-pointer' />
      </nav>
      <div style={{ marginRight: 16, marginTop: 0 }} className='dark-mode'>
        <Switch
          checked={Utils.getFlagsmithHasFeature('dark_mode')}
          onChange={this.toggleDarkMode}
          onMarkup='Light'
          offMarkup='Dark'
        />
      </div>
      <div className='org-nav'>
        <a id='logout-link' href='#' onClick={AppActions.logout} className='popover-bt__list-item'>
          <img src='/static/images/icons/aside/logout-dark.svg' className='mr-2' />
          Logout
        </a>
      </div>
    </>
  )

  renderMainContent = (user, pageHasAside, isMobile, asideIsVisible, projectId, environmentId, projectNotLoaded, logoImgLocation, isHomepage) => (
    <div>
      <div className={pageHasAside ? `aside-body${isMobile && !asideIsVisible ? '-full-width' : ''}` : ''}>
        {!isHomepage && (!pageHasAside || !asideIsVisible || !isMobile) && this.renderNavbar(user, pageHasAside, isMobile, asideIsVisible, logoImgLocation, projectId)}
        {pageHasAside && <Aside projectId={projectId} environmentId={environmentId} toggleAside={this.toggleAside} asideIsVisible={asideIsVisible} />}
        {isMobile && pageHasAside && asideIsVisible ? null : (
          <div>
            {projectNotLoaded ? (
              <div className='text-center'>
                <Loader />
              </div>
            ) : (
              this.props.children
            )}
          </div>
        )}
      </div>
    </div>
  )

  render() {
    if (
      Utils.getFlagsmithHasFeature('dark_mode') &&
      !document.body.classList.contains('dark')
    ) {
      document.body.classList.add('dark')
    }
    let logoImgLocation = document.body.classList.contains('dark')
      ? '/static/images/vizio-logo-white.svg'
      : '/static/images/vizio-logo-black.svg'
    const { location } = this.props
    const pathname = location.pathname
    const { asideIsVisible, totalApiCalls } = this.state
    const match = matchPath(pathname, {
      exact: false,
      path: '/project/:projectId/environment/:environmentId',
      strict: false,
    })
    const match2 = matchPath(pathname, {
      exact: false,
      path: '/project/:projectId',
      strict: false,
    })
    const projectId =
      _.get(match, 'params.projectId') || _.get(match2, 'params.projectId')
    const environmentId = _.get(match, 'params.environmentId')
    const pageHasAside = environmentId || projectId
    const isHomepage =
      pathname === '/' ||
      pathname === '/login' ||
      pathname === '/signup' ||
      pathname.includes('/invite')
    if (Project.amplitude) {
      amplitude.getInstance().init(Project.amplitude)
    }
    if (
      AccountStore.getOrganisation() &&
      AccountStore.getOrganisation().block_access_to_admin
    ) {
      return <Blocked />
    }
    if (Project.maintenance || this.props.error || !window.projectOverrides) {
      return <Maintenance />
    }
    if (this.props.isLoading) {
      return (
        <AccountProvider
          onNoUser={this.onNoUser}
          onLogout={this.onLogout}
          onLogin={this.onLogin}
        >
          {() => (
            <div id='login-page'>
              <AppLoader />
            </div>
          )}
        </AccountProvider>
      )
    }
    const projectNotLoaded =
      !ProjectStore.model && document.location.href.includes('project/')
    if (document.location.href.includes('widget')) {
      return <div>{this.props.children}</div>
    }

    return (
      <Provider store={getStore()}>
        <AccountProvider
          onNoUser={this.onNoUser}
          onLogout={this.onLogout}
          onLogin={this.onLogin}
        >
          {({ isSaving, organisation, user }, { twoFactorLogin }) =>
            user && user.twoFactorPrompt
              ? this.renderTwoFactorPrompt(isSaving, twoFactorLogin)
              : this.renderMainContent(user, pageHasAside, isMobile, asideIsVisible, projectId, environmentId, projectNotLoaded, logoImgLocation, isHomepage)
          }
        </AccountProvider>
      </Provider>
    )
  }
}

App.propTypes = {
  history: RequiredObject,
  location: RequiredObject,
}

export default withRouter(ConfigProvider(App))

if (E2E) {
  const e2e = document.getElementsByClassName('e2e')
  if (e2e && e2e[0]) {
    e2e[0].classList.toggle('display-none')
  }
}
