import { PureComponent } from 'react'
import Format from 'common/utils/format'
import watchfreeLogo from '../static/images/watchfree.png'
import viziogramLogo from '../static/images/viziogram.png'
import viziohomeLogo from '../static/images/loki.png'
import avodLogo from '../static/images/avod.webp'
import oobeLogo from '../static/images/oobe.png'

const AsideProjectButton = class extends PureComponent {
  static displayName = 'AsideProjectButton'

  componentDidMount() {
    this.props.active()
  }

  render() {
    const truncated = Format.truncateText(this.props.name, 26)
    const children = (
      <div
        className='aside__projects-item clickable'
        data-test={this.props['data-test']}
        onClick={this.props.onClick}
      >
        <div className='flex-row justify-content-center'>
          <div className='flex-column'>
            {(this.props.name === 'watchfree' && (
              <img src={watchfreeLogo} className={this.props.className} />
            )) ||
              (this.props.name === 'viziogram' && (
                <img src={viziogramLogo} className={this.props.className} />
              )) ||
              (this.props.name === 'home' && (
                <img src={viziohomeLogo} className={this.props.className} />
              )) ||
              (this.props.name === 'avod' && (
                <img src={avodLogo} className={this.props.className} />
              )) ||
              (this.props.name === 'oobe' && (
                <img src={oobeLogo} className={this.props.className} />
              )) || (
                <ButtonProject className={this.props.className}>
                  {this.props.projectLetter}
                </ButtonProject>
              )}

            {/* <ButtonProject className={this.props.className}>
              {this.props.projectLetter}
            </ButtonProject> */}
          </div>
          <div className='flex-column'>
            <p className={`aside__projects-item-title ${this.props.className}`}>
              {truncated}
            </p>
          </div>
        </div>
      </div>
    )
    return truncated === this.props.name ? (
      children
    ) : (
      <Tooltip title={children} place='right'>
        {this.props.name}
      </Tooltip>
    )
  }
}

AsideProjectButton.displayName = 'AsideProjectButton'

// Card.propTypes = {
//     title: oneOfType([OptionalObject, OptionalString]),
//     icon: OptionalString,
//     children: OptionalNode,
// };

module.exports = AsideProjectButton
