import data from 'common/data/base/_data';
import ProjectStore from 'common/stores/project-store';
import FeatureListStore from 'common/stores/feature-list-store';

export default (WrappedComponent) => {
  class HOC extends React.Component {
    static displayName = 'withFoo';

    constructor(props) {
      super(props);
      ES6Component(this);
      this.listenTo(FeatureListStore, 'saved', () => {
        this.getOverrides();
      });
      this.state = {};
    }

    componentDidMount() {
      this.getOverrides();
    }

    getOverrides = () => {
      if (this.props.projectFlag) {
        Promise.all([
          this.fetchFeatureSegments(),
          this.fetchFeatureStates(),
        ]).then(this.processOverrides);
      }
    };

    fetchFeatureSegments = () => {
      const environmentId = ProjectStore.getEnvironmentIdFromKey(this.props.environmentId);
      return data.get(`${Project.api}features/feature-segments/?environment=${environmentId}&feature=${this.props.projectFlag.id}`);
    };

    fetchFeatureStates = () => {
      const environmentId = ProjectStore.getEnvironmentIdFromKey(this.props.environmentId);
      return data.get(`${Project.api}features/featurestates/?environment=${environmentId}&feature=${this.props.projectFlag.id}`);
    };

    processOverrides = ([segmentRes, featureStatesRes]) => {
      const results = segmentRes.results;
      const featureStates = featureStatesRes.results;
      const environmentOverride = featureStatesRes.results.find(v => !v.feature_segment && !v.identity);

      featureStates.forEach(f => {
        if (f.feature_segment) {
          const index = _.findIndex(results, { id: f.feature_segment });
          if (index !== -1) {
            results[index].value = Utils.featureStateToValue(f.feature_state_value);
            results[index].enabled = f.enabled;
            results[index].feature_segment_value = f;
            results[index].multivariate_options = f.multivariate_feature_state_values;

            const multiVariates = featureStatesRes.results.find(mv => mv.feature_segment === f.feature_segment);
            results[index].multivariate_feature_state_values = (multiVariates && multiVariates.multivariate_feature_state_values) || [];
          }
        }
      });

      const resResults = segmentRes.results || [];
      const segmentOverrides = results.concat(
        (this.props.newSegmentOverrides || []).map((v, i) => ({
          ...v,
          priority: resResults.length + i,
        }))
      );

      this.setState({
        environmentVariations: environmentOverride?.multivariate_feature_state_values || [],
        originalSegmentOverrides: _.cloneDeep(segmentOverrides),
        segmentOverrides,
      });
    };

    onEnvironmentVariationsChange = (environmentVariations) => {
      this.setState({ environmentVariations });
    };

    removeMultivariateOption = (id) => {
      this.setState(prevState => ({
        segmentOverrides: prevState.segmentOverrides.map(v => ({
          ...v,
          multivariate_options: v.multivariate_options?.filter(m => m.multivariate_feature_option !== id),
        })),
      }));
    };

    updateSegments = (segmentOverrides) => {
      this.setState({ segmentOverrides });
    };

    render() {
      return (
        <WrappedComponent
          ref="wrappedComponent"
          updateSegments={this.updateSegments}
          onEnvironmentVariationsChange={this.onEnvironmentVariationsChange}
          removeMultivariateOption={this.removeMultivariateOption}
          {...this.props}
          {...this.state}
        />
      );
    }
  }

  return HOC;
};
