import React, { PureComponent } from 'react';
import propTypes from 'prop-types';
import cn from 'classnames';

export default class Paging extends PureComponent {
  static displayName = 'Paging';
  static propTypes = {
    goToPage: propTypes.func,
    isLoading: propTypes.bool,
    onNextClick: propTypes.func,
    onPreviousClick: propTypes.func,
    paging: propTypes.object,
    prevPage: propTypes.func, 
    nextPage: propTypes.func,
  };

  handlePageClick = (index) => {
    const { goToPage, paging } = this.props;
    if (paging.currentPage !== index + 1) {
      goToPage(index + 1);
    }
  };

  renderPageButton = (index, isActive) => (
    <button
      key={index}
      className={cn({ 'active': isActive, page: true })}
      onClick={() => this.handlePageClick(index)}
      aria-pressed={isActive}
      tabIndex={0}
    >
      {index + 1}
    </button>
  );

  renderDots = () => <div className={cn({ page: true })}>...</div>;

  render() {
    const {
      props: { isLoading, paging, prevPage, nextPage },
    } = this;
    const currentIndex = paging.currentPage - 1;
    const lastPage = Math.ceil(paging.count / paging.pageSize);
    const spaceBetween = 2;
    const from = Math.max(0, currentIndex + 1 - spaceBetween);
    const to = Math.min(lastPage, (currentIndex || currentIndex + 1) + spaceBetween);
    const range = _.range(from, to);
    const noPages = range.length < 2;

    if (noPages && !(paging.next || paging.previous)) {
      return <div />;
    }

    return (
      <Row className="list-item paging py-0" style={isLoading ? { opacity: 0.5 } : {}}>
        <Button
          disabled={isLoading || !paging.previous}
          className="icon btn-paging ion-ios-arrow-back"
          onClick={() => prevPage()}
        />
        {paging.currentPage ? (
          <Row className="list-item">
            {from > 0 && this.renderPageButton(0, currentIndex === 0)}
            {from > 0 && this.renderDots()}
            {range.map((index) => this.renderPageButton(index, currentIndex === index))}
            {to < lastPage && this.renderDots()}
            {to < lastPage && this.renderPageButton(lastPage - 1, currentIndex === lastPage - 1)}
          </Row>
        ) : (
          !!paging.page && (
            <span>
              Page {paging.page}
              {paging.pageSize && paging.count ? ` of ${Math.ceil(paging.count / paging.pageSize)}` : ''}
            </span>
          )
        )}
        <Button
          className="icon btn-paging ion-ios-arrow-forward"
          disabled={isLoading || !paging.next}
          onClick={() => nextPage()}
        />
      </Row>
    );
  }
}
