import React, { Component } from 'react'
import TagValues from './tags/TagValues'
import HistoryIcon from './HistoryIcon'
import ConfirmToggleFeature from './modals/ConfirmToggleFeature'
import ConfirmRemoveFeature from './modals/ConfirmRemoveFeature'
import CreateFlagModal from './modals/CreateFlag'
import ProjectStore from 'common/stores/project-store'
import Permission from 'common/providers/Permission'
import Constants from 'common/constants'
import { hasProtectedTag } from 'common/utils/hasProtectedTag'
import SegmentsIcon from './svg/SegmentsIcon'
import UsersIcon from './svg/UsersIcon' // we need this to make JSX compile

class TheComponent extends Component {
  static propTypes = {
    environmentId: propTypes.string.isRequired, // Added prop validation for environmentId
    projectFlag: propTypes.object.isRequired,
    environmentFlags: propTypes.object.isRequired,
    projectId: propTypes.string.isRequired,
    permission: propTypes.bool.isRequired,
    toggleFlag: propTypes.func.isRequired,
    removeFlag: propTypes.func.isRequired,
    hideRemove: propTypes.bool,
    hideAudit: propTypes.bool,
    condensed: propTypes.bool,
    isCompareEnv: propTypes.bool,
    fadeEnabled: propTypes.bool,
    fadeValue: propTypes.bool,
    widget: propTypes.bool,
    readOnly: propTypes.bool,
  };
  
  static contextTypes = {
    router: propTypes.object.isRequired,
  }

  state = {}

  confirmToggle = (projectFlag, environmentFlag, cb) => {
    openModal(
      'Toggle Feature',
      <ConfirmToggleFeature
        environmentId={this.props.environmentId}
        projectFlag={projectFlag}
        environmentFlag={environmentFlag}
        cb={cb}
      />,
    )
  }

  componentDidMount() {
    const { environmentFlags, projectFlag } = this.props
    const { feature, tab } = Utils.fromParam()
    const { id } = projectFlag
    if (`${id}` === feature && typeof environmentFlags[id] !== 'undefined') {
      this.editFeature(projectFlag, environmentFlags[id], tab)
    }

    try {
      AsyncStorage.getItem('lastEnv').then((res) => {
        if (res) {
          const lastEnv = JSON.parse(res)
          this.setState({
            projectName: lastEnv.projectName,
          })
        }
      })
    } catch (e) {
      console.error(e)
    }
  }

  confirmRemove = (projectFlag, cb) => {
    openModal(
      'Remove Feature',
      <ConfirmRemoveFeature
        environmentId={this.props.environmentId}
        projectFlag={projectFlag}
        projectName={this.state.projectName}
        cb={cb}
      />,
    )
  }

  editFeature = (projectFlag, environmentFlag, tab) => {
    API.trackEvent(Constants.events.VIEW_FEATURE)

    history.replaceState(
      {},
      null,
      `${document.location.pathname}?feature=${projectFlag.id}${
        tab ? `&tab=${tab}` : ''
      }`,
    )
    openModal(
      `${this.props.permission ? 'Edit Feature' : 'Feature'}: ${
        projectFlag.name
      }`,
      <CreateFlagModal
        isEdit
        router={this.context.router}
        environmentId={this.props.environmentId}
        projectId={this.props.projectId}
        projectFlag={projectFlag}
        noPermissions={!this.props.permission}
        environmentFlag={environmentFlag}
        flagId={environmentFlag.id}
      />,
      null,
      {
        className: 'side-modal fade create-feature-modal',
        onClose: () => {
          history.replaceState({}, null, `${document.location.pathname}`)
        },
      },
    )
  }

  render() {
    const {
      environmentFlags,
      environmentId,
      permission,
      projectFlag,
      projectFlags,
      projectId,
      toggleFlag,
      removeFlag,
    } = this.props;
    
    const { created_date, description, id, name } = projectFlag;
    const readOnly = this.props.readOnly || Utils.getFlagsmithHasFeature('read_only_mode');
    const isProtected = hasProtectedTag(projectFlag, projectId);
    const environment = ProjectStore.getEnvironment(environmentId);
    const changeRequestsEnabled = Utils.changeRequestsEnabled(
      environment && environment.minimum_change_request_approvals
    );
  
    const handleToggle = (flagId, envFlag) => {
      if (changeRequestsEnabled) {
        this.editFeature(projectFlag, envFlag);
        return;
      }
      
      this.confirmToggle(
        projectFlag,
        envFlag,
        (environments) => {
          toggleFlag(
            _.findIndex(projectFlags, { id: flagId }),
            environments
          );
        }
      );
    };
  
    const renderSwitch = (flagId, envFlag) => (
      <Switch
        disabled={!permission || readOnly}
        data-test={`feature-switch-${this.props.index}${envFlag && envFlag.enabled ? '-on' : '-off'}`}
        checked={envFlag && envFlag.enabled}
        onChange={() => handleToggle(flagId, envFlag)}
      />
    );
  
    const renderFeatureValue = (flagId, envFlag) => (
      <FeatureValue
        onClick={() => permission && !readOnly && this.editFeature(projectFlag, envFlag)}
        value={envFlag && envFlag.feature_state_value}
        data-test={`feature-value-${this.props.index}`}
      />
    );
  
    if (this.props.condensed) {
      return Utils.renderWithPermission(
        permission,
        Constants.environmentPermissions('Write'),
        <Row
          onClick={() => !readOnly && this.editFeature(projectFlag, environmentFlags[id])}
          style={{
            flexDirection: this.props.isCompareEnv ? 'column' : 'row',
            overflow: 'hidden',
            ...(this.props.style || {}),
          }}
        >
          <div className={`mr-2 ${this.props.fadeEnabled && 'faded'}`}>
            {renderSwitch(id, environmentFlags[id])}
          </div>
          <div
            className={`mr-2 clickable ${this.props.fadeValue && 'faded'}`}
            style={this.props.isCompareEnv && { marginTop: '5px' }}
          >
            {renderFeatureValue(id, environmentFlags[id])}
          </div>
        </Row>
      );
    }
  
    return Utils.renderWithPermission(
      permission,
      Constants.environmentPermissions('Write'),
      <Row
        style={{ flexWrap: 'nowrap' }}
        className={`list-item ${readOnly ? '' : 'clickable'} ${
          this.props.widget ? 'py-1' : 'py-2'
        }`}
        key={id}
        space
        data-test={`feature-item-${this.props.index}`}
      >
        <div
          className="flex flex-1"
          onClick={() => !readOnly && this.editFeature(projectFlag, environmentFlags[id])}
        >
          <div>
            <Row>
              <ButtonLink className={`mr-2 ${readOnly ? 'cursor-default' : ''}`}>
                {name}
              </ButtonLink>
              {this.renderFeatureTags(projectFlag)}
              <TagValues projectId={`${projectId}`} value={projectFlag.tags} />
            </Row>
            <span className="text-small text-muted">
              Created {moment(created_date).format('Do MMM YYYY HH:mma')}
              {' - '}
              {description || 'No description'}
            </span>
          </div>
        </div>
        <Row>
          <Row style={{ marginBottom: 5, marginRight: 15, marginTop: 5 }}>
            <Column>
              {renderFeatureValue(id, environmentFlags[id])}
            </Column>
            <Column>
              {renderSwitch(id, environmentFlags[id])}
            </Column>
          </Row>
  
          {this.renderAuditLog(projectId, environmentId, environment, projectFlag)}
          {this.renderRemoveFeatureButton(projectFlag, isProtected, permission, readOnly, projectId, removeFlag)}
        </Row>
      </Row>
    );
  }
  

  renderFeatureTags = (projectFlag) => {
    return (
      <>
        {projectFlag.owners && !!projectFlag.owners.length ? (
          <Tooltip
            title={
              <ButtonLink>
                <span className='ion ion-md-person pr-2' />
              </ButtonLink>
            }
            place='right'
          >
            {`Flag assigned to ${projectFlag.owners
              .map((v) => `${v.first_name} ${v.last_name}`)
              .join(', ')}`}
          </Tooltip>
        ) : (
          <span />
        )}
        {!!projectFlag.num_segment_overrides && (
          <Tooltip
            title={
              <span className='chip ml-1 chip--sm bg-primary text-white'>
                <SegmentsIcon className='chip-svg-icon' />
                <span>{projectFlag.num_segment_overrides}</span>
              </span>
            }
            place='top'
          >
            {`${projectFlag.num_segment_overrides} Segment Override${
              projectFlag.num_segment_overrides !== 1 ? 's' : ''
            }`}
          </Tooltip>
        )}
        {!!projectFlag.num_identity_overrides && (
          <Tooltip
            title={
              <span className='chip ml-1 chip--sm bg-primary text-white'>
                <UsersIcon className='chip-svg-icon' />
                <span>{projectFlag.num_identity_overrides}</span>
              </span>
            }
            place='top'
          >
            {`${projectFlag.num_identity_overrides} Identity Override${
              projectFlag.num_identity_overrides !== 1 ? 's' : ''
            }`}
          </Tooltip>
        )}
      </>
    );
  };

  renderAuditLog = (projectId, environmentId, environment, projectFlag) => {
    return (
      AccountStore.getOrganisationRole() === 'ADMIN' &&
        !this.props.hideAudit && (
          <Tooltip
            html
            title={
              <button
                onClick={() => {
                  this.context.router.history.push(
                    `/project/${projectId}/environment/${environmentId}/audit-log?env=${environment.id}&search=${projectFlag.name}`,
                  )
                }}
                className='btn btn--with-icon'
                data-test={`feature-history-${this.props.index}`}
              >
                <HistoryIcon />
              </button>
            }
          >
            Feature history
          </Tooltip>
        )
    );
  };

  renderRemoveFeatureButton = (projectFlag, isProtected, permission, readOnly, projectId, removeFlag) => {
    return (
      !this.props.hideRemove && (
        <Permission
          level='project'
          permission='DELETE_FEATURE'
          id={projectId}
        >
          {({ permission: removeFeaturePermission }) =>
            Utils.renderWithPermission(
              removeFeaturePermission,
              Constants.projectPermissions('Admin'),
              <Column>
                <Tooltip
                  html
                  title={
                    <button
                      disabled={
                        !removeFeaturePermission || readOnly || isProtected
                      }
                      onClick={() =>
                        this.confirmRemove(projectFlag, () => {
                          removeFlag(projectId, projectFlag)
                        })
                      }
                      className='btn btn--with-icon'
                      data-test={`remove-feature-btn-${this.props.index}`}
                    >
                      <RemoveIcon />
                    </button>
                  }
                >
                  {isProtected
                    ? '<span>This feature has been tagged as <bold>protected</bold>, <bold>permanent</bold>, <bold>do not delete</bold>, or <bold>read only</bold>. Please remove the tag before attempting to delete this flag.</span>'
                    : 'Remove feature'}
                </Tooltip>
              </Column>,
            )
          }
        </Permission>
      )
    );
  };
}

export default TheComponent
