import React from 'react'
import VariationValue from './VariationValue'
import ValueEditor from 'components/ValueEditor'
import AutosizeInput from 'react-input-autosize'

export default function VariationOptions({
  controlValue,
  disabled,
  duplicateVariation,
  isEdit,
  multivariateOptions,
  preventRemove,
  readOnlyValue,
  removeVariation,
  segments,
  select,
  setValue,
  setVariations,
  updateVariation,
  variationOverrides,
  weightTitle,
}) {
  const invalid = multivariateOptions.length && controlValue < 0
  if (!multivariateOptions || !multivariateOptions.length) {
    return null
  }
  const controlSelected =
    !variationOverrides ||
    !variationOverrides.find((v) => v.percentage_allocation === 100)

  return (
    <>
      {invalid && (
        <div className='alert alert-danger'>
          Your variation percentage splits total to over 100%
        </div>
      )}

      {select && (
        <div className='panel panel--flat panel-without-heading mb-2'>
          <div className='panel-content'>
            <Row>
              <Flex>
                <ValueEditor
                  disabled
                  value={Utils.getTypedValue(controlValue)}
                />
              </Flex>
              <div
                onMouseDown={(e) => {
                  e.stopPropagation()
                  setVariations([])
                  setValue(controlValue)
                }}
                className={`btn--radio ion ${
                  controlSelected
                    ? 'ion-ios-radio-button-on'
                    : 'ion-ios-radio-button-off'
                }`}
              />
            </Row>
          </div>
        </div>
      )}
      {multivariateOptions.map((theValue, i) => {
        const override = select
          ? variationOverrides &&
            variationOverrides[0] &&
            typeof variationOverrides[0].multivariate_feature_option_index ===
              'number'
            ? i === variationOverrides[0].multivariate_feature_option_index &&
              variationOverrides[0]
            : variationOverrides &&
              variationOverrides.find(
                (v) => v.multivariate_feature_option === theValue.id,
              )
          : variationOverrides &&
            variationOverrides.find((v) => v.percentage_allocation === 100)
        return select ? (
          <div className='panel panel--flat panel-without-heading multivariate-panel mb-2'>
            <div className='panel-content'>
              <AutosizeInput
                value={theValue.name}
                className='pr-2 py-2 font-weight-bold'
                disabled={disabled || readOnlyValue}
                onChange={(e) => {
                  onChange({
                    ...value,
                    name: Utils.safeParseEventValue(e),
                  })
                }}
                inputStyle={{
                  border: 0,
                  paddingTop: '8px',
                  paddingBottom: '8px',
                }}
                placeholder='Variation Name'
              />
              <Row>
                <Flex>
                  <ValueEditor
                    disabled={true}
                    value={Utils.getTypedValue(
                      Utils.featureStateToValue(theValue),
                    )}
                  />
                </Flex>
                <div
                  data-test={`select-variation-${Utils.featureStateToValue(
                    theValue,
                  )}`}
                  onMouseDown={(e) => {
                    e.stopPropagation()
                    e.preventDefault()
                    setVariations([
                      {
                        multivariate_feature_option: theValue.id,
                        multivariate_feature_option_index: i,
                        percentage_allocation: 100,
                      },
                    ])
                    return false
                  }}
                  className={`btn--radio ion ${
                    override
                      ? 'ion-ios-radio-button-on'
                      : 'ion-ios-radio-button-off'
                  }`}
                />
              </Row>
            </div>
          </div>
        ) : (
          <VariationValue
            segments={segments}
            key={i}
            index={i}
            isEdit={isEdit}
            duplicateVariation={duplicateVariation}
            readOnlyValue={readOnlyValue}
            preventRemove={preventRemove || disabled}
            value={theValue}
            onChange={(e) => {
              updateVariation(i, e, variationOverrides)
            }}
            weightTitle={weightTitle}
            disabled={disabled}
            onRemove={
              preventRemove || disabled
                ? null
                : () => removeVariation(i, theValue.id)
            }
          />
        )
      })}
    </>
  )
}