import React, { FC, useState } from 'react'
import { RouterChildContext } from 'react-router'
import { Link, Redirect } from 'react-router-dom'
import { useHasPermission } from 'common/providers/Permission'
import ConfigProvider from 'common/providers/ConfigProvider'
import Constants from 'common/constants'
import {
  useDeleteIdentityMutation,
  useGetIdentitiesQuery,
} from 'common/services/useIdentity'
import useSearchThrottle from 'common/useSearchThrottle'
import { Req } from 'common/types/requests'
import { Identity } from 'common/types/responses'
import CreateUserModal from 'components/modals/CreateUser'
import RemoveIcon from 'components/RemoveIcon'
import PanelSearch from 'components/PanelSearch'
import Button, { ButtonLink } from 'components/base/forms/Button' // we need this to make JSX compile
import JSONReference from 'components/JSONReference' // we need this to make JSX compile
import Utils from 'common/utils/utils'

const CodeHelp = require('../CodeHelp')

type UsersPageType = {
  router: RouterChildContext['router']
  match: {
    params: {
      environmentId: string
      projectId: string
    }
  }
}
const UsersPage: FC<UsersPageType> = (props) => {
  const [page, setPage] = useState<{
    number: number
    pageType: Req['getIdentities']['pageType']
    pages: Req['getIdentities']['pages']
  }>({ number: 1, pageType: undefined, pages: undefined })

  const { search, searchInput, setSearchInput } = useSearchThrottle(
    Utils.fromParam().search,
    () => {
      setPage({
        number: 1,
        pageType: undefined,
        pages: undefined,
      })
    },
    12
  )
  const [deleteIdentity] = useDeleteIdentityMutation({})
  const isEdge = Utils.getIsEdge()

  const { data: identitiesCount } = useGetIdentitiesQuery({
    environmentId: props.match.params.environmentId,
    isEdge,
    page: page.number,
    countOnly: true
  });

  const { data: identities, isLoading } = useGetIdentitiesQuery({
    environmentId: props.match.params.environmentId,
    isEdge,
    page: page.number,
    pageType: page.pageType,
    page_size: 10,
    pages: page.pages,
    search,
  })

  const { environmentId } = props.match.params

  const { permission } = useHasPermission({
    id: environmentId,
    level: 'environment',
    permission: Utils.getViewIdentitiesPermission(),
  })

  const removeIdentity = (id: string, identifier: string) => {
    openConfirm(
      <h3>Delete User</h3>,
      <p>
        {'Are you sure you want to delete '}
        <strong>{identifier}</strong>
        {'?'}
      </p>,
      () => deleteIdentity({ environmentId, id, isEdge: Utils.getIsEdge() }),
    )
  }

  const newUser = () => {
    openModal(
      'New Users',
      <CreateUserModal environmentId={environmentId} />,
      null,
      { className: 'alert fade expand' },
    )
  }

  const renderRow = (
    { id, identifier, identity_uuid }: Identity,
    index: number,
  ) => {
    let linkTo = '';
    // @ts-ignore
    if (search !== '' && identities?.results?.length === 1 && identities.results[0].identifier === search){
      linkTo = `users/${encodeURIComponent(identifier)}/${id}`;
    }
    if (permission) {
      // Auto-navigate when a result is found
      // @ts-ignore
      if (search !== '' && identities?.results?.length === 1 && identities.results[0].identifier === search){
          return <Redirect to={linkTo} />
      }
      return null;
    } else {
      return (
        <Row
          space
          className='list-item'
          key={id}
          data-test={`user-item-${index}`}
        >
          {identifier}
        </Row>
      );
    }
  };

  return (
    <div className='app-container container' id='user-page' data-test='user-page'>
      <div>
        <div>
          <Row>
            <Flex>
              <div>
                <h3>Vizio TV</h3>
                <p>
                  View and manage features states for individual users. This
                  will override individual default feature settings for your
                  selected app.{' '}
                </p>
                <p>Total no of devices: <b>{ identitiesCount?.count }</b></p>
              </div>
            </Flex>
          </Row>
        </div>
        <FormGroup>
          <div>
            <FormGroup>
              <PanelSearch
                renderSearchWithNoResults
                renderFooter={() => (
                  <JSONReference
                    className='mx-2 mt-4'
                    title={'Users'}
                    json={identities?.results}
                  />
                )}
                id='users-list'
                title='Vizio TV'
                className='no-pad'
                isLoading={isLoading}
                filterLabel={Utils.getIsEdge() ? 'Starts with' : 'Contains'}
                icon='ion-md-person'
                items={search !== '' ? identities?.results : null}
                nextPage={() => {
                  setPage({
                    number: page.number + 1,
                    pageType: 'NEXT',
                    pages: identities?.last_evaluated_key
                      ? (page.pages || []).concat([
                          identities?.last_evaluated_key,
                        ])
                      : undefined,
                  })
                }}
                prevPage={() => {
                  setPage({
                    number: page.number - 1,
                    pageType: 'PREVIOUS',
                    pages: page.pages
                      ? Utils.removeElementFromArray(
                          page.pages,
                          page.pages.length - 1,
                        )
                      : undefined,
                  })
                }}
                goToPage={(newPage: number) => {
                  setPage({
                    number: newPage,
                    pageType: undefined,
                    pages: undefined,
                  })
                }}
                renderRow={renderRow}
                renderNoResults={
                  <div>
                    {search ? (
                      <span style={{ display: 'inline-block', padding: '1rem' }}>
                        You have no users in your project
                        {' '}
                        for <strong>"{search}"</strong>
                      </span>
                    ) : (
                      <span style={{ display: 'inline-block', padding: '1rem' }}>Search to find the device id</span>
                    )}
                  </div>
                }
                filterRow={() => true}
                search={searchInput}
                onChange={(e: InputEvent) => {
                  setSearchInput(Utils.safeParseEventValue(e))
                }}
              />
            </FormGroup>
          </div>
        </FormGroup>
      </div>
    </div>
  )
}

export default ConfigProvider(UsersPage)