import React, { useState } from 'react'
import classNames from 'classnames'
import sanitizeHtml from 'sanitize-html'

import ValueEditor from 'components/ValueEditor' // we need this to make JSX compile
import Constants from 'common/constants'
import AutosizeInput from 'react-input-autosize'
import EditIcon from 'components/svg/EditIcon'
import CopyIcon from 'components/svg/CopyIcon'

const VariationValue = ({
  disabled,
  duplicateVariation,
  index,
  isEdit,
  onChange,
  onRemove,
  readOnlyValue,
  segments = false,
  value,
  weightTitle,
}) => {
  const [valid, setValid] = useState(true)
  const disableValue = isEdit && !!value.custom_id

  const validateValue = (value) => {
    if (typeof value === 'string' && value.trim().length > 0) {
      return true
    }
    return false
  }

  return (
    <div className='panel panel--flat panel-without-heading'>
      <div className='panel-content variation-value multivariate-panel'>
        <Row>
          <div
            className='variation-weight flex-row'
            style={{ alignItems: 'baseline' }}
          >
            <AutosizeInput
              value={value.name}
              className='pr-2 py-2 font-weight-bold'
              disabled={disabled || readOnlyValue}
              onChange={(e) => {
                onChange({
                  ...value,
                  name: Utils.safeParseEventValue(e),
                })
              }}
              inputStyle={{
                border: 0,
                paddingTop: '8px',
                paddingBottom: '8px',
              }}
              placeholder='Variation Name'
            />
            {!(disabled || readOnlyValue) && (
              <EditIcon height={18} width={18} />
            )}
          </div>
        </Row>
        <Row>
          <div className='flex flex-1'>
            <InputGroup
              component={
                <ValueEditor
                  data-test={`featureVariationValue${index}`}
                  name='featureValue'
                  className={classNames({
                    'full-width': true,
                    'variation-value-invalid': !valid,
                  })}
                  isValid={false}
                  value={Utils.getTypedValue(Utils.featureStateToValue(value))}
                  disabled={disabled || readOnlyValue || disableValue}
                  onChange={(e) => {
                    const safeValue = E2E
                      ? e.target.value
                      : Utils.safeParseEventValue(
                          sanitizeHtml(e, {
                            allowedAttributes: {},
                            allowedTags: [],
                          }),
                        )


                    setValid(validateValue(safeValue))
                    onChange({
                      ...value,
                      ...Utils.valueToFeatureState(safeValue),
                      name: value.name,
                    });
                  }}
                  placeholder="e.g. 'big' "
                />
              }
              tooltip={Constants.strings.REMOTE_CONFIG_DESCRIPTION_VARIATION}
              title='Variation Value*'
            />
          </div>
          <div className='variation-weight ml-2' style={{ width: 210 }}>
            <InputGroup
              type='number'
              data-test={`featureVariationWeight${index}`}
              id={`featureVariationWeight${index}`}
              onChange={(e) => {
                const providedValue = parseInt(Utils.safeParseEventValue(e))
                onChange({
                  ...value,
                  default_percentage_allocation: Utils.safeParseEventValue(e)
                    ? providedValue !== null &&
                      providedValue <= 100 &&
                      providedValue >= 0
                      ? providedValue
                      : value.default_percentage_allocation
                    : value.default_percentage_allocation,
                })
              }}
              value={value.default_percentage_allocation}
              inputProps={{
                maxLength: 3,
                readOnly: disabled,
                style: { marginTop: 2 },
              }}
              title={weightTitle}
            />
          </div>
          {!segments && (
            <div className='ml-2'>
              <Tooltip
                title={
                  <button
                    onClick={() =>
                      duplicateVariation(Utils.featureStateToValue(value))
                    }
                    id='copy-multivariate'
                    type='button'
                    className='btn btn--with-icon ml-auto'
                  >
                    <CopyIcon width={27} height={27} />
                  </button>
                }
                place='right'
              >
                Duplicate
              </Tooltip>
            </div>
          )}
          {!!onRemove && (
            <div className='ml-2' style={{ width: 30 }}>
              <button
                onClick={onRemove}
                data-test={`deleteMultivariate${index}`}
                id={`deleteMultivariate${index}`}
                type='button'
                className='btn btn--with-icon ml-auto btn--remove'
              >
                <RemoveIcon />
              </button>
            </div>
          )}
        </Row>
      </div>
    </div>
  )
}

export default VariationValue