import React from 'react'

if (typeof hljs !== 'undefined') {
  hljs.initHighlightingOnLoad()
}
function escapeHtml(unsafe) {
  if (!unsafe || !unsafe.__html) return unsafe
  return {
    __html: `${unsafe.__html}`
      .replace(/&/g, '&amp;')
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')
      .replace(/"/g, '&quot;')
      .replace(/'/g, '&#039;'),
  }
}
const defaultValue = { __html: 'Enter a value...' }
const defaultDisabledValue = { __html: ' ' }
const collapsedHeight = 110
class Highlight extends React.Component {
  state = {
    value: { __html: this.props.children },
  }

  constructor(props) {
    super(props)
    this.setEl = this.setEl.bind(this)
  }

  componentDidMount() {
    this.highlightCode()
  }

  componentDidUpdate(prevProps) {
    this.highlightCode()
    if (this.props.className !== prevProps.className) {
      setTimeout(() => {
        this.highlightCode()
      }, 100)
    }
    if (this.state.prevValue !== this.props.children) {
      this.setState({
        key: Date.now(),
        value: {
          ...this.state.value,
          __html: this.props.children,
        },
      })
    }
  }

  highlightCode = () => {
    const nodes = this.el.querySelectorAll('pre code')
    if (nodes[0] && nodes[0].innerHTML && nodes[0].innerHTML.match(/[<>]/)) {
      if (!this.state.focus) {
        nodes[0].innerHTML = nodes[0].innerText
      }
      setTimeout(() => {
        this.highlightCode()
      }, 100)
      return
    }
    if (typeof hljs !== 'undefined') {
      for (let i = 0; i < nodes.length; i++) {
        hljs.highlightBlock(nodes[i])
      }
    }
  }

  setEl(el) {
    this.el = el
    this.measure = (force) => {
      if (this.props.forceExpanded) return
      if (!this.el) return
      const height = this.el.clientHeight
      if (!this.state.expandable && height > collapsedHeight) {
        this.setState({ expandable: true, expanded: false })
      }
      if (typeof this.state.expandable !== 'boolean' || force) {
        if (height > collapsedHeight) {
          this.setState({ expandable: true, expanded: false })
        } else if (!height) {
          setTimeout(() => {
            this.measure()
          }, 50)
        } else {
          this.setState({ expandable: false })
        }
      }
    }
    this.measure()
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (nextState.focus !== this.state.focus) return true
    if (nextProps.className !== this.props.className) return true
    if (nextState.expandable !== this.state.expandable) return true
    if (nextState.expanded !== this.state.expanded) return true
    if (nextProps['data-test'] !== this.props['data-test']) return true
    return this.state.value.__html !== `${nextProps.children}`
  }

  _handleInput = (event) => {
    const value = event.target.innerText
    this.setState({ value: { __html: value } }, () => {
      event.persist()
      this.props.onChange(value)
    })
  }

  onFocus = () => {
    this.setState({ focus: true })
  }

  onBlur = () => {
    this.setState({ focus: false })
  }

  render() {
    const { children, className, disabled, element: Element, innerHTML } = this.props;
    const props = { className, ref: this.setEl };
  
    // Handle innerHTML scenario
    if (innerHTML) {
      return this.renderWithInnerHTML(props, Element, children);
    }
  
    // Handle Element rendering
    if (Element) {
      return this.renderWithElement(props, Element, children);
    }
  
    // Determine HTML content for the code block
    const html = this.getHtmlContent(disabled, children);
  
    // Render the main component
    return (
      <div className={this.state.expandable ? 'expandable' : ''}>
        {this.renderPreBlock(props, html)}
        {this.renderExpandButton()}
      </div>
    );
  }
  
  // Helper method to handle rendering when innerHTML is used
  renderWithInnerHTML(props, Element, children) {
    props.dangerouslySetInnerHTML = { __html: children };
    return Element ? <Element {...props} /> : <div {...props} />;
  }
  
  // Helper method to handle rendering when an Element is provided
  renderWithElement(props, Element, children) {
    return <Element {...props}>{children}</Element>;
  }
  
  // Helper method to get the HTML content
  getHtmlContent(disabled, children) {
    const { focus, value } = this.state;
    const { preventEscape } = this.props;
    const defaultHtml = children ? { ...value } : disabled ? defaultDisabledValue : defaultValue;
  
    return preventEscape
      ? focus ? value : defaultHtml
      : escapeHtml(focus ? value : defaultHtml);
  }
  
  // Helper method to render the pre block
  renderPreBlock(props, html) {
    return (
      <pre
        style={this.getPreBlockStyle()}
        ref={this.setEl}
      >
        <code
          style={this.props.style}
          data-test={this.props['data-test']}
          contentEditable={!!this.props.onChange}
          onBlur={this.onBlur}
          onFocus={this.onFocus}
          onInput={this._handleInput}
          className={`${this.props.className} ${this.isEmpty() ? 'empty' : ''}`}
          dangerouslySetInnerHTML={html}
        />
      </pre>
    );
  }
  
  // Helper method to render the expand button if expandable
  renderExpandButton() {
    if (this.state.expandable) {
      return (
        <div className="expand text-center mt-2">
          <ButtonLink
            onClick={this.toggleExpand}
            className="btn--link-primary"
          >
            {this.state.expanded ? 'Hide' : 'Show More'}
            <span className={`icon ml-2 ion text-primary ${this.state.expanded ? 'ion-ios-arrow-up' : 'ion-ios-arrow-down'}`} />
          </ButtonLink>
        </div>
      );
    }
    return null;
  }
  
  // Helper method to toggle the expanded state
  toggleExpand = () => {
    this.setState({ expanded: !this.state.expanded });
  }
  
  // Helper method to check if content is empty
  isEmpty() {
    return !this.state.value || !this.state.value.__html;
  }
  
  // Helper method to get the pre block styles
  getPreBlockStyle() {
    return {
      ...(this.props.style || {}),
      height: this.state.expanded || !this.state.expandable ? 'auto' : collapsedHeight,
      opacity: typeof this.state.expandable === 'boolean' || this.props.forceExpanded ? 1 : 0,
    };
  }
  
}

Highlight.defaultProps = {
  className: null,
  element: null,
  innerHTML: false,
}

export default Highlight
